<!--
Unified Recipe Editor - Single panel interface for managing recipe packs
Combines pack management, machine list, recipe list, and recipe editor
-->
<script setup lang="ts">
import type {GameItem, GameRecipe} from '#types/game-data';
import type {CustomRecipe, CustomRecipeIO} from '@/scripts/custom-recipe-service';
import type {RecipePack} from '@/scripts/recipe-pack-service';
import {injectGameData} from '@/scripts/data';
import {customRecipeService} from '@/scripts/custom-recipe-service';
import {recipePackService, customRecipeToPackRecipe, packRecipeToCustomRecipe, type RecipePackRecipe} from '@/scripts/recipe-pack-service';
import {injectSettings} from '@/scripts/settings';
import {mdiRefresh, mdiPlus, mdiContentSave, mdiContentSaveEdit, mdiTrashCan, mdiFileUpload, mdiFileDownload, mdiClose, mdiMagnify, mdiContentCopy, mdiChevronRight, mdiCog, mdiClipboardText, mdiClipboardCheck, mdiPlusCircle, mdiMinusCircle, mdiPencil} from '@mdi/js';
import {computed, ref, watch, onMounted, onUnmounted, nextTick, unref} from 'vue';
import {useDebounceFn} from '@vueuse/core';

interface Props {
    modelValue: boolean;
}

interface Emits {
    (e: 'update:modelValue', value: boolean): void;
    (e: 'recipes-changed'): void;
}

const props = defineProps<Props>();
const emit = defineEmits<Emits>();

const gameData = injectGameData();
const settings = injectSettings();

// Dialog state
const dialog = ref(false);
const fileInput = ref<HTMLInputElement | null>(null);
const showFileInput = ref(false);

// Pack management
const activePack = ref<RecipePack | null>(null);
const packName = ref('');
const packAuthor = ref('');
const packDescription = ref('');
const packFileName = ref('custom_recipes.json');

// Machine list
const machineSearchQuery = ref('');
const selectedMachine = ref<GameItem | null>(null);

// Recipe list
const selectedRecipe = ref<CustomRecipe | GameRecipe | null>(null);
const recipeSearchQuery = ref('');

// Recipe editor
const isEditingNew = ref(false);
const editRecipeName = ref('');
const editRecipeLongName = ref('');
const editRecipeTime = ref(10);
const editRecipePowerMultiplier = ref(100);
const editRecipeId = ref('');
const editInputs = ref<CustomRecipeIO[]>([]);
const editOutputs = ref<CustomRecipeIO[]>([]);

// Item dialog
const itemDialog = ref(false);
const itemDialogType = ref<'input' | 'output'>('input');
const selectedItem = ref<GameItem | null>(null);
const itemQuantity = ref(1);
const specialItemType = ref<number | null>(null);
const flagHasProbability = ref(false);
const flagHideInMenu = ref(false);
const flagHideOnWindow = ref(false);
const flagRoundToCeil = ref(false);

// State for editing existing items
const editingItemIndex = ref<number | null>(null);
const editingItemType = ref<'input' | 'output'>('input');

// Item search state
const itemSearch = ref('');
const filteredItems = ref<GameItem[]>([]);
const pageSize = 10;
const page = ref(1);

// Blueprint
const blueprintText = ref('');
const showBlueprint = ref(false);

// Special item types
const specialItemTypes = [
    {label: '', value: 1, icon: 'mdi-lightning-bolt'},
    {label: '', value: 2, icon: 'mdi-cog'},
    {label: '', value: 3, icon: 'mdi-chip'},
    {label: '', value: 4, icon: 'mdi-flask'},
    {label: '', value: 5, icon: 'mdi-wrench'},
    {label: '', value: 6, icon: 'mdi-cloud'},
    {label: '', value: 7, icon: 'mdi-account-hard-hat'},
];

watch(() => props.modelValue, (newVal) => {
    dialog.value = newVal;
    if (newVal) {
        initializePack();
    }
});

watch(dialog, (newVal) => {
    emit('update:modelValue', newVal);
});

// Subscribe to pack service changes
let unsubscribePack: (() => void) | null = null;
let isUpdatingPack = false;

onMounted(() => {
    unsubscribePack = recipePackService.subscribe(() => {
        // Guard against recursive updates
        if (!dialog.value || isUpdatingPack) {
            return;
        }

        const servicePack = recipePackService.getActivePack();
        if (servicePack) {
            isUpdatingPack = true;
            // Create a new copy to trigger Vue reactivity
            activePack.value = {
                ...servicePack,
                Recipes: [...servicePack.Recipes]
            };
            // Reset guard after update is processed
            nextTick(() => {
                isUpdatingPack = false;
            });
        }
    });
});

onUnmounted(() => {
    unsubscribePack?.();
});

// Initialize pack from localStorage or create new
function initializePack() {
    const storedPack = recipePackService.getActivePack();
    if (storedPack) {
        // Create a new copy to trigger Vue reactivity
        activePack.value = {
            ...storedPack,
            Recipes: [...storedPack.Recipes]
        };
        packName.value = storedPack.PackName;
        packAuthor.value = storedPack.Author || '';
        packDescription.value = storedPack.Description || '';
        packFileName.value = `${storedPack.PackName}.json`;
    } else {
        // Create default pack
        createNewPack();
    }
}

// Computed properties
const filteredMachines = computed(() => {
    const machines = gameData.gameFactoriesArray.filter(item =>
        item.recipeDictionary && !item.isAbstractClassItem
    );
    if (!machineSearchQuery.value.trim()) return machines;
    const query = machineSearchQuery.value.toLowerCase();
    return machines.filter(m =>
        (m.label && m.label.toLowerCase().includes(query)) ||
        (m.name && m.name.toLowerCase().includes(query))
    );
});

const baseRecipesForMachine = computed(() => {
    if (!selectedMachine.value) return [];
    const recipeDict = gameData.getItemRecipeDictionary(selectedMachine.value);
    const allRecipes = Array.from(recipeDict.recipesMap.values());
    // 
    return allRecipes.filter(recipe => !gameData.isCustomRecipe(recipe.name));
});

const customRecipesForMachine = computed(() => {
    if (!selectedMachine.value) return [];
    const machineId = selectedMachine.value.recipeDictionary?.name || selectedMachine.value.name;
    return activePack.value?.Recipes.filter(r => r.recipeDictionary === machineId) || [];
});

const allMachines = computed(() => gameData.gameFactoriesArray.filter(item =>
    item.recipeDictionary && !item.isAbstractClassItem
));

const availableItems = computed(() => {
    let items = gameData.gameItemsArray.filter(item =>
        !item.isAbstractClassItem
    );

    // Filter buildings if setting is enabled
    if (settings.filterBuildingsInCustomRecipe) {
        items = items.filter(item => !item.recipeDictionary);
    }

    return items;
});

// Pagination and search
const pages = computed(() => (Math.ceil(unref(filteredItems).length / pageSize) || 1));
const currentPage = computed(() => {
    const start = (unref(page) - 1) * pageSize;
    return unref(filteredItems).slice(start, start + pageSize);
});

const updateSearch = useDebounceFn(() => {
    let val = unref(itemSearch);
    val = val && val.trim();
    if(!val) {
        filteredItems.value = availableItems.value;
        page.value = 1;
        return;
    }
    const searchText = val.toLowerCase().split(/\s+/).map((s) => s.trim());
    filteredItems.value = availableItems.value.filter((item) => (
        searchText.every((l) => !l || (item.lowerLabel.indexOf(l) > -1)))
    );
    if(unref(page) > unref(pages)) {
        page.value = unref(pages);
    }
}, 200, {maxWait: 500});

watch(itemSearch, (value, oldValue) => {
    if(value != oldValue)
        updateSearch();
});

// Initialize filtered items
watch(availableItems, (items) => {
    filteredItems.value = items;
}, {immediate: true});

// Pack management functions
function createNewPack() {
    const newPack = recipePackService.createNewPack('');
    activePack.value = newPack;
    packName.value = newPack.PackName;
    packAuthor.value = '';
    packDescription.value = '';
    packFileName.value = 'custom_recipes.json';
    recipePackService.setActivePack(newPack);
    clearEditor();
}

function updatePackInfo() {
    if (!activePack.value) return;
    recipePackService.updatePackMetadata({
        PackName: packName.value,
        Author: packAuthor.value,
        Description: packDescription.value,
    });
    packFileName.value = `${packName.value}.json`;
}

function savePackToFile() {
    if (!activePack.value) return;
    updatePackInfo();
    recipePackService.saveToFile(activePack.value, packFileName.value);
}

function loadPackFromFile() {
    showFileInput.value = false;
    setTimeout(() => {
        showFileInput.value = true;
        setTimeout(() => {
            fileInput.value?.click();
        });
    });
}

async function handleFileSelect(event: Event) {
    const target = event.target as HTMLInputElement;
    const files = target.files;
    if (!files || !files[0]) return;

    try {
        const pack = await recipePackService.loadFromFile(files[0]);
        // Create a new copy to trigger Vue reactivity
        activePack.value = {
            ...pack,
            Recipes: [...pack.Recipes]
        };
        packName.value = pack.PackName;
        packAuthor.value = pack.Author || '';
        packDescription.value = pack.Description || '';
        packFileName.value = files[0].name;

        //  setActivePack  customRecipeService
        recipePackService.setActivePack(pack);

        //  recipes-changed 
        emit('recipes-changed');

        clearEditor();
    } catch (error) {
        console.error('Failed to load pack:', error);
        alert('');
    }
}

function deletePack() {
    if (!activePack.value) return;
    if (confirm(` "${activePack.value.PackName}" `)) {
        const packToDelete = activePack.value;

        //  customRecipeService 
        for (const recipe of packToDelete.Recipes) {
            customRecipeService.deleteRecipe(recipe.name);
        }

        //  dynamicGameData 
        if ('removeCustomRecipes' in gameData) {
            const recipeNames = packToDelete.Recipes.map(r => r.name);
            (gameData as any).removeCustomRecipes(recipeNames);
        }

        // 
        recipePackService.deletePackFromStorage(packToDelete.PackName);

        //  recipes-changed 
        emit('recipes-changed');

        createNewPack();
    }
}

// Machine selection
function selectMachine(machine: GameItem) {
    selectedMachine.value = machine;
    selectedRecipe.value = null;
}

// Recipe selection
function selectBaseRecipe(recipe: GameRecipe) {
    selectedRecipe.value = recipe;
    loadRecipeToEditor(recipe, false);
}

function selectCustomRecipe(recipe: CustomRecipe) {
    selectedRecipe.value = recipe;
    loadRecipeToEditor(recipe, true);
}

function copyFromBase(recipe: GameRecipe) {
    isEditingNew.value = true;
    editRecipeName.value = `Custom_${recipe.name}`;
    editRecipeLongName.value = `${recipe.longName || recipe.name} ()`;
    editRecipeTime.value = recipe.time;
    editRecipePowerMultiplier.value = 100;
    editRecipeId.value = '()';
    editInputs.value = recipe.input.map(io => ({...io}));
    editOutputs.value = recipe.output.map(io => ({...io}));
    selectedRecipe.value = null;
}

function loadRecipeToEditor(recipe: GameRecipe | CustomRecipe, isCustom: boolean) {
    isEditingNew.value = false;

    if (isCustom) {
        const customRecipe = recipe as CustomRecipe;
        editRecipeName.value = customRecipe.name;
        editRecipeLongName.value = customRecipe.longName || '';
        editRecipeTime.value = customRecipe.time;
        editRecipePowerMultiplier.value = 100;
        editRecipeId.value = customRecipe.name;
        editInputs.value = [...customRecipe.input];
        editOutputs.value = [...customRecipe.output];
    } else {
        const baseRecipe = recipe as GameRecipe;
        editRecipeName.value = baseRecipe.name;
        editRecipeLongName.value = baseRecipe.longName || '';
        editRecipeTime.value = baseRecipe.time;
        editRecipePowerMultiplier.value = 100;
        editRecipeId.value = '()';
        editInputs.value = baseRecipe.input.map(io => ({...io}));
        editOutputs.value = baseRecipe.output.map(io => ({...io}));
    }
}

// Recipe editor functions
function clearEditor() {
    isEditingNew.value = true;
    editRecipeName.value = '';
    editRecipeLongName.value = '';
    editRecipeTime.value = 10;
    editRecipePowerMultiplier.value = 100;
    editRecipeId.value = '()';
    editInputs.value = [];
    editOutputs.value = [];
    selectedRecipe.value = null;
}

function saveRecipe() {
    if (!selectedMachine.value || !activePack.value) return;

    const machineId = selectedMachine.value.recipeDictionary?.name || selectedMachine.value.name;

    const customRecipe: CustomRecipe = {
        name: isEditingNew.value ? recipePackService.generateRecipeId(machineId) : editRecipeName.value,
        longName: editRecipeLongName.value || undefined,
        input: editInputs.value,
        output: editOutputs.value,
        time: editRecipeTime.value,
        recipeDictionary: machineId,
    };

    recipePackService.addRecipe(customRecipe);
    editRecipeId.value = customRecipe.name;
    isEditingNew.value = false;

    // Also update the custom recipe service for runtime
    const existing = customRecipeService.getRecipe(customRecipe.name);
    if (existing) {
        customRecipeService.updateRecipe(customRecipe);
    } else {
        customRecipeService.addRecipe(customRecipe);
    }

    emit('recipes-changed');
    selectedRecipe.value = customRecipe;
}

async function deleteCustomRecipe(recipe: CustomRecipe) {
    if (!confirm(` "${recipe.longName || recipe.name}" `)) return;

    // Delete from customRecipeService
    customRecipeService.deleteRecipe(recipe.name);

    // DIRECTLY remove from dynamicGameData cache
    // This clears: customRecipes Map, injectedRecipes Map, and modifiedRecipeDictionaries cache
    if ('removeCustomRecipes' in gameData) {
        (gameData as any).removeCustomRecipes([recipe.name]);
    }

    // Wait for services to process
    await nextTick();

    // Then delete from recipePackService
    recipePackService.removeRecipe(recipe.name);

    emit('recipes-changed');

    if (selectedRecipe.value?.name === recipe.name) {
        clearEditor();
    }
}

// Item dialog functions
function openAddItem(type: 'input' | 'output') {
    itemDialogType.value = type;
    editingItemIndex.value = null;  // Reset editing state
    editingItemType.value = 'input';
    selectedItem.value = null;
    itemQuantity.value = 1;
    resetSpecialParameters();
    itemDialog.value = true;
}

function resetSpecialParameters() {
    specialItemType.value = null;
    flagHasProbability.value = false;
    flagHideInMenu.value = false;
    flagHideOnWindow.value = false;
    flagRoundToCeil.value = false;
}

function editItem(type: 'input' | 'output', index: number) {
    const items = type === 'input' ? editInputs.value : editOutputs.value;
    const item = items[index];

    editingItemType.value = type;
    editingItemIndex.value = index;
    itemDialogType.value = type;

    // Pre-fill the dialog with existing item data
    selectedItem.value = gameData.getGameItem(item.name) || null;
    itemQuantity.value = item.count;

    // Set flags
    const flags = item.flags || 0;
    flagHasProbability.value = !!(flags & 1);
    flagHideInMenu.value = !!(flags & 4);
    flagHideOnWindow.value = !!(flags & 8);
    flagRoundToCeil.value = !!(flags & 16);

    // Determine special item type from item name
    specialItemType.value = getSpecialItemTypeFromName(item.name);

    itemDialog.value = true;
}

function getFlags(): number {
    let flags = 0;
    if (flagHasProbability.value) flags |= 1;
    if (flagHideInMenu.value) flags |= 4;
    if (flagHideOnWindow.value) flags |= 8;
    if (flagRoundToCeil.value) flags |= 16;
    return flags;
}

function addItem() {
    if (!selectedItem.value) return;

    const io: CustomRecipeIO = {
        name: selectedItem.value.name,
        count: itemQuantity.value,
        flags: getFlags(),
    };

    // Check if we're editing an existing item
    if (editingItemIndex.value !== null && editingItemType.value === itemDialogType.value) {
        // Update existing item
        if (itemDialogType.value === 'input') {
            editInputs.value[editingItemIndex.value] = io;
        } else {
            editOutputs.value[editingItemIndex.value] = io;
        }
    } else {
        // Add new item
        if (itemDialogType.value === 'input') {
            editInputs.value.push(io);
        } else {
            editOutputs.value.push(io);
        }
    }

    // Reset editing state
    editingItemIndex.value = null;
    editingItemType.value = 'input';
    itemDialog.value = false;
}

function removeItem(type: 'input' | 'output', index: number) {
    if (type === 'input') {
        editInputs.value.splice(index, 1);
    } else {
        editOutputs.value.splice(index, 1);
    }
}

function getFlagLabel(flags: number): string[] {
    const labels: string[] = [];
    if (flags & 1) labels.push('');
    if (flags & 4) labels.push('');
    if (flags & 8) labels.push('');
    if (flags & 16) labels.push('');
    return labels;
}

function getSpecialItemTypeFromName(name: string): number | null {
    const specialItemMap: Record<string, number> = {
        'Electricity': 1,
        'MechPower': 2,
        'Computing': 3,
        'Research': 4,
        'Maintenance': 5,
        'Pollution': 6,
        'Worker': 7,
    };
    return specialItemMap[name] || null;
}

// Blueprint functions
function exportBlueprint() {
    if (!selectedMachine.value) return;
    const blueprint = {
        machine: selectedMachine.value.recipeDictionary?.name || selectedMachine.value.name,
        name: editRecipeLongName.value || editRecipeName.value,
        time: editRecipeTime.value,
        powerMultiplier: editRecipePowerMultiplier.value,
        inputs: editInputs.value,
        outputs: editOutputs.value,
    };
    blueprintText.value = JSON.stringify(blueprint, null, 2);
    showBlueprint.value = true;
}

function importBlueprint() {
    if (!blueprintText.value.trim()) return;
    try {
        const blueprint = JSON.parse(blueprintText.value);
        if (blueprint.name) editRecipeLongName.value = blueprint.name;
        if (blueprint.time) editRecipeTime.value = blueprint.time;
        if (blueprint.powerMultiplier) editRecipePowerMultiplier.value = blueprint.powerMultiplier;
        if (blueprint.inputs) editInputs.value = blueprint.inputs;
        if (blueprint.outputs) editOutputs.value = blueprint.outputs;
        showBlueprint.value = false;
        blueprintText.value = '';
    } catch (error) {
        alert('');
    }
}

function copyRecipeId() {
    navigator.clipboard.writeText(editRecipeId.value);
}

// Recipe display helpers
function formatRecipeIO(io: CustomRecipeIO | {name: string; count: number}): string {
    const item = gameData.getGameItem(io.name);
    const label = item?.label || io.name;
    return `${label} x${io.count}`;
}

// Filter out special items (with HideInMenu or HideOnWindow flags) from recipe list display
function filterVisibleItems(items: (CustomRecipeIO | {name: string; count: number; flags?: number})[]): (CustomRecipeIO | {name: string; count: number; flags?: number})[] {
    return items.filter(io => {
        const flags = (io as CustomRecipeIO).flags || 0;
        // Hide items with HideInMenu (4) or HideOnWindow (8) flags
        return !(flags & 4) && !(flags & 8);
    });
}

function getRecipeDisplayClass(recipe: GameRecipe | CustomRecipe): string {
    const isSelected = selectedRecipe.value === recipe;
    return isSelected ? 'bg-primary' : '';
}
</script>

<template>
    <v-dialog v-model="dialog" fullscreen persistent>
        <v-card class="h-100 d-flex flex-column">
            <!-- Top: Recipe Pack Management -->
            <v-card-text class="pa-4 border-b">
                <div class="d-flex align-center ga-4">
                    <div class="d-flex align-center ga-2 flex-grow-1">
                        <span class="text-subtitle-2">:</span>
                        <v-chip size="small" color="primary">{{ packFileName }}</v-chip>
                        <v-btn
                            size="small"
                            :icon="mdiRefresh"
                            variant="text"
                            @click="initializePack"
                        />
                    </div>

                    <div class="d-flex align-center ga-2">
                        <v-text-field
                            v-model="packName"
                            label=""
                            density="compact"
                            hide-details
                            style="width: 150px"
                        />
                        <v-text-field
                            v-model="packAuthor"
                            label=""
                            density="compact"
                            hide-details
                            clearable
                            style="width: 120px"
                        />
                        <v-text-field
                            v-model="packDescription"
                            label=""
                            density="compact"
                            hide-details
                            clearable
                            style="width: 200px"
                        />
                    </div>

                    <v-btn size="small" :prepend-icon="mdiPlus" @click="createNewPack">
                        
                    </v-btn>
                    <v-btn size="small" :prepend-icon="mdiContentSave" @click="savePackToFile">
                        
                    </v-btn>
                    <v-btn size="small" :prepend-icon="mdiFileUpload" @click="loadPackFromFile">
                        
                    </v-btn>
                    <v-btn size="small" :prepend-icon="mdiTrashCan" color="error" @click="deletePack">
                        
                    </v-btn>
                    <v-btn size="small" :prepend-icon="mdiContentSaveEdit" color="primary" @click="updatePackInfo">
                        
                    </v-btn>
                    <v-btn size="small" :icon="mdiClose" @click="dialog = false" />
                </div>
            </v-card-text>

            <!-- Main Content: Three Column Layout -->
            <v-card-text class="flex-grow-1 pa-0 overflow-hidden">
                <div class="d-flex h-100">
                    <!-- Left: Machine List -->
                    <div class="border-r pa-4" style="width: 250px; overflow-y: auto">
                        <div class="text-subtitle-2 mb-2"></div>
                        <v-text-field
                            v-model="machineSearchQuery"
                            :prepend-icon="mdiMagnify"
                            label="..."
                            density="compact"
                            hide-details
                            clearable
                            class="mb-2"
                        />
                        <v-list density="compact" class="overflow-y-auto" style="max-height: calc(100vh - 250px)">
                            <v-list-item
                                v-for="machine in filteredMachines"
                                :key="machine.name"
                                @click="selectMachine(machine)"
                                :class="selectedMachine === machine ? 'bg-primary' : ''"
                                class="cursor-pointer"
                            >
                                <template #prepend>
                                    <icon-component :image="machine.name" size="24" />
                                </template>
                                <v-list-item-title class="text-caption">{{ machine.label }}</v-list-item-title>
                            </v-list-item>
                        </v-list>
                    </div>

                    <!-- Middle: Recipe List -->
                    <div class="border-r pa-4" style="width: 500px; overflow-y: auto">
                        <div class="d-flex align-center justify-space-between mb-2">
                            <span class="text-subtitle-2"></span>
                            <v-chip size="small">: {{ baseRecipesForMachine.length }}, : {{ customRecipesForMachine.length }}</v-chip>
                        </div>

                        <div v-if="!selectedMachine" class="text-center text-grey mt-8">
                            
                        </div>

                        <div v-else>
                            <!-- Base Game Recipes -->
                            <div class="mb-4">
                                <div class="text-caption text-grey mb-2"></div>
                                <v-list density="compact">
                                    <v-list-item
                                        v-for="recipe in baseRecipesForMachine"
                                        :key="recipe.name"
                                        @click="selectBaseRecipe(recipe)"
                                        :class="getRecipeDisplayClass(recipe)"
                                        class="cursor-pointer"
                                    >
                                        <div class="d-flex align-center ga-1">
                                            <span class="text-caption font-weight-bold">{{ recipe.time }}</span>
                                            <span v-for="(io, i) in filterVisibleItems(recipe.input)" :key="`in-${i}`" class="d-flex align-center">
                                                <icon-component :image="io.name" size="16" />
                                                <span class="text-caption">{{ io.count }}</span>
                                                <span v-if="i < filterVisibleItems(recipe.input).length - 1" class="text-caption">+</span>
                                            </span>
                                            <v-icon :icon="mdiChevronRight" size="16" />
                                            <span v-for="(io, i) in filterVisibleItems(recipe.output)" :key="`out-${i}`" class="d-flex align-center">
                                                <icon-component :image="io.name" size="16" />
                                                <span class="text-caption">{{ io.count }}</span>
                                            </span>
                                            <v-spacer />
                                            <v-btn
                                                size="x-small"
                                                :icon="mdiContentCopy"
                                                variant="text"
                                                @click.stop="copyFromBase(recipe)"
                                            />
                                        </div>
                                    </v-list-item>
                                </v-list>
                            </div>

                            <!-- Custom Recipes -->
                            <div>
                                <div class="text-caption text-grey mb-2"></div>
                                <v-list density="compact">
                                    <v-list-item
                                        v-for="recipe in customRecipesForMachine"
                                        :key="recipe.name"
                                        @click="selectCustomRecipe(recipe)"
                                        :class="getRecipeDisplayClass(recipe)"
                                        class="cursor-pointer"
                                    >
                                        <div class="d-flex align-center ga-1">
                                            <span class="text-caption font-weight-bold">{{ recipe.time }}</span>
                                            <span v-for="(io, i) in filterVisibleItems(recipe.input)" :key="`in-${i}`" class="d-flex align-center">
                                                <icon-component :image="io.name" size="16" />
                                                <span class="text-caption">{{ io.count }}</span>
                                                <span v-if="i < filterVisibleItems(recipe.input).length - 1" class="text-caption">+</span>
                                            </span>
                                            <v-icon :icon="mdiChevronRight" size="16" />
                                            <span v-for="(io, i) in filterVisibleItems(recipe.output)" :key="`out-${i}`" class="d-flex align-center">
                                                <icon-component :image="io.name" size="16" />
                                                <span class="text-caption">{{ io.count }}</span>
                                            </span>
                                            <v-spacer />
                                            <v-btn
                                                size="x-small"
                                                :icon="mdiTrashCan"
                                                color="error"
                                                variant="text"
                                                @click.stop="deleteCustomRecipe(recipe)"
                                            />
                                        </div>
                                        <div class="text-caption text-grey">{{ recipe.longName || recipe.name }}</div>
                                    </v-list-item>
                                    <v-list-item v-if="customRecipesForMachine.length === 0">
                                        <v-list-item-title class="text-caption text-grey text-center">
                                            
                                        </v-list-item-title>
                                    </v-list-item>
                                </v-list>
                            </div>
                        </div>
                    </div>

                    <!-- Right: Recipe Editor -->
                    <div class="pa-4 flex-grow-1" style="overflow-y: auto">
                        <div class="d-flex align-center justify-space-between mb-4">
                            <span class="text-subtitle-2"></span>
                            <div class="d-flex ga-2">
                                <v-btn size="small" @click="clearEditor"></v-btn>
                                <v-btn size="small" color="primary" :prepend-icon="mdiContentSave" @click="saveRecipe">
                                    
                                </v-btn>
                            </div>
                        </div>

                        <v-row class="mb-2">
                            <v-col cols="6">
                                <v-select
                                    v-model="selectedMachine"
                                    :items="allMachines"
                                    item-title="label"
                                    return-object
                                    label=""
                                    density="compact"
                                    hide-details
                                >
                                    <template #selection="{item}">
                                        <div class="d-flex align-center">
                                            <icon-component :image="item.raw.name" class="mr-2" />
                                            {{ item.raw.label }}
                                        </div>
                                    </template>
                                    <template #item="{item, props}">
                                        <v-list-item v-bind="props">
                                            <template #prepend>
                                                <icon-component :image="item.raw.name" />
                                            </template>
                                        </v-list-item>
                                    </template>
                                </v-select>
                            </v-col>
                            <v-col cols="6">
                                <v-text-field
                                    v-model="editRecipeLongName"
                                    label=""
                                    density="compact"
                                    hide-details
                                    placeholder="..."
                                />
                            </v-col>
                        </v-row>

                        <v-row class="mb-2">
                            <v-col cols="6">
                                <v-text-field
                                    v-model.number="editRecipeTime"
                                    type="number"
                                    label=""
                                    density="compact"
                                    hide-details
                                />
                            </v-col>
                            <v-col cols="6">
                                <div class="d-flex align-center">
                                    <span class="text-caption mr-2">ID:</span>
                                    <code class="text-caption flex-grow-1">{{ editRecipeId }}</code>
                                    <v-btn
                                        v-if="editRecipeId && editRecipeId !== '()' && editRecipeId !== '()'"
                                        size="x-small"
                                        :icon="mdiContentCopy"
                                        variant="text"
                                        @click="copyRecipeId"
                                    />
                                </div>
                            </v-col>
                        </v-row>

                        <!-- Advanced Options -->
                        <v-expansion-panels class="mb-4">
                            <v-expansion-panel>
                                <v-expansion-panel-title class="text-caption">
                                    <v-icon :icon="mdiCog" class="mr-2" />
                                    
                                </v-expansion-panel-title>
                                <v-expansion-panel-text>
                                    <v-row>
                                        <v-col cols="6">
                                            <v-text-field
                                                v-model.number="editRecipePowerMultiplier"
                                                type="number"
                                                label="%"
                                                density="compact"
                                                hide-details
                                                suffix="%"
                                            />
                                        </v-col>
                                    </v-row>
                                </v-expansion-panel-text>
                            </v-expansion-panel>
                        </v-expansion-panels>

                        <!-- Blueprint -->
                        <v-expansion-panels class="mb-4">
                            <v-expansion-panel>
                                <v-expansion-panel-title class="text-caption">
                                    <v-icon :icon="mdiClipboardText" class="mr-2" />
                                    
                                </v-expansion-panel-title>
                                <v-expansion-panel-text>
                                    <v-textarea
                                        v-model="blueprintText"
                                        label="..."
                                        rows="3"
                                        density="compact"
                                        hide-details
                                    />
                                    <div class="d-flex ga-2 mt-2">
                                        <v-btn size="small" :prepend-icon="mdiClipboardCheck" @click="importBlueprint">
                                            
                                        </v-btn>
                                        <v-btn size="small" :prepend-icon="mdiClipboardText" @click="exportBlueprint">
                                            
                                        </v-btn>
                                    </div>
                                </v-expansion-panel-text>
                            </v-expansion-panel>
                        </v-expansion-panels>

                        <!-- Inputs -->
                        <div class="mb-4">
                            <div class="d-flex align-center justify-space-between mb-2">
                                <span class="text-caption font-weight-bold"></span>
                                <v-btn size="small" :prepend-icon="mdiPlusCircle" @click="openAddItem('input')">
                                    
                                </v-btn>
                            </div>
                            <v-list density="compact" bg-color="grey-lighten-4">
                                <v-list-item v-for="(item, index) in editInputs" :key="`in-${index}`">
                                    <template #prepend>
                                        <icon-component :image="item.name" />
                                    </template>
                                    <v-list-item-title>{{ formatRecipeIO(item) }}</v-list-item-title>
                                    <v-list-item-subtitle v-if="item.flags">
                                        <v-chip v-for="(label, i) in getFlagLabel(item.flags || 0)" :key="i" size="x-small">
                                            {{ label }}
                                        </v-chip>
                                    </v-list-item-subtitle>
                                    <template #append>
                                        <v-btn
                                            size="x-small"
                                            :icon="mdiPencil"
                                            variant="text"
                                            @click="editItem('input', index)"
                                        />
                                        <v-btn size="x-small" :icon="mdiMinusCircle" color="error" @click="removeItem('input', index)" />
                                    </template>
                                </v-list-item>
                                <v-list-item v-if="editInputs.length === 0">
                                    <v-list-item-title class="text-caption text-grey text-center">
                                        
                                    </v-list-item-title>
                                </v-list-item>
                            </v-list>
                        </div>

                        <!-- Outputs -->
                        <div>
                            <div class="d-flex align-center justify-space-between mb-2">
                                <span class="text-caption font-weight-bold"></span>
                                <v-btn size="small" :prepend-icon="mdiPlusCircle" @click="openAddItem('output')">
                                    
                                </v-btn>
                            </div>
                            <v-list density="compact" bg-color="success-lighten-4">
                                <v-list-item v-for="(item, index) in editOutputs" :key="`out-${index}`">
                                    <template #prepend>
                                        <icon-component :image="item.name" />
                                    </template>
                                    <v-list-item-title>{{ formatRecipeIO(item) }}</v-list-item-title>
                                    <v-list-item-subtitle v-if="item.flags">
                                        <v-chip v-for="(label, i) in getFlagLabel(item.flags || 0)" :key="i" size="x-small">
                                            {{ label }}
                                        </v-chip>
                                    </v-list-item-subtitle>
                                    <template #append>
                                        <v-btn
                                            size="x-small"
                                            :icon="mdiPencil"
                                            variant="text"
                                            @click="editItem('output', index)"
                                        />
                                        <v-btn size="x-small" :icon="mdiMinusCircle" color="error" @click="removeItem('output', index)" />
                                    </template>
                                </v-list-item>
                                <v-list-item v-if="editOutputs.length === 0">
                                    <v-list-item-title class="text-caption text-grey text-center">
                                        
                                    </v-list-item-title>
                                </v-list-item>
                            </v-list>
                        </div>
                    </div>
                </div>
            </v-card-text>
        </v-card>
    </v-dialog>

    <!-- File Input -->
    <input
        v-if="showFileInput"
        ref="fileInput"
        type="file"
        accept=".json"
        class="d-none"
        @change="handleFileSelect"
    />

    <!-- Item Dialog -->
    <v-dialog v-model="itemDialog" max-width="500px">
        <v-card>
            <v-card-title>{{ editingItemIndex !== null ? '' : '' }}{{ itemDialogType === 'input' ? '' : '' }}</v-card-title>
            <v-card-text>
                <v-autocomplete
                    v-model="selectedItem"
                    v-model:search="itemSearch"
                    :items="currentPage"
                    item-title="label"
                    return-object
                    clearable
                    no-filter
                    hide-details
                    label=""
                    density="compact"
                    @focus="(page = 1)"
                >
                    <template #item="{item, props}">
                        <v-list-item v-bind="props">
                            <template #prepend>
                                <icon-component :image="item.raw.name" />
                            </template>
                        </v-list-item>
                    </template>
                    <template #selection="{item}">
                        <div class="d-flex align-center">
                            <icon-component :image="item.raw.name" class="mr-2" />
                            {{ item.raw.label }}
                        </div>
                    </template>
                    <template #append-item>
                        <v-pagination v-model="page" density="compact" :length="pages" :total-visible="5" />
                    </template>
                </v-autocomplete>

                <v-text-field
                    v-model.number="itemQuantity"
                    type="number"
                    label=""
                    density="compact"
                />

                <v-select
                    v-model="specialItemType"
                    :items="specialItemTypes"
                    item-title="label"
                    item-value="value"
                    label=""
                    density="compact"
                    clearable
                >
                    <template #selection="{item}">
                        <div v-if="item" class="d-flex align-center">
                            <v-icon>{{ `mdi-${item.raw.icon}` }}</v-icon>
                            <span class="ml-2">{{ item.raw.label }}</span>
                        </div>
                    </template>
                    <template #item="{item, props}">
                        <v-list-item v-bind="props">
                            <template #prepend>
                                <v-icon>{{ `mdi-${item.raw.icon}` }}</v-icon>
                            </template>
                        </v-list-item>
                    </template>
                </v-select>

                <v-expansion-panels v-if="specialItemType" class="mt-2">
                    <v-expansion-panel>
                        <v-expansion-panel-title class="text-caption"></v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <v-checkbox-btn v-model="flagHasProbability" label="" density="compact" />
                            <v-checkbox-btn v-model="flagHideInMenu" label="" density="compact" />
                            <v-checkbox-btn v-model="flagHideOnWindow" label="" density="compact" />
                            <v-checkbox-btn v-model="flagRoundToCeil" label="" density="compact" />
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                </v-expansion-panels>
            </v-card-text>
            <v-card-actions>
                <v-spacer />
                <v-btn @click="itemDialog = false"></v-btn>
                <v-btn color="primary" :disabled="!selectedItem" @click="addItem">
                    {{ editingItemIndex !== null ? '' : '' }}
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

