Author: Alexey Usov (dax@xdax.ru, https://github.com/doubleaxe)
Copy Recipe Dialog - Copy from built-in recipes
-->
<script setup lang="ts">
import type {GameItem, GameRecipe} from '#types/game-data';
import {injectGameData} from '@/scripts/data';
import {mdiClose, mdiContentCopy, mdiFilter, mdiFactory} from '@mdi/js';
import {computed, ref, watch} from 'vue';

interface Props {
    modelValue: boolean;
}

interface Emits {
    (e: 'update:modelValue', value: boolean): void;
    (e: 'copy', recipe: GameRecipe, factoryName: string): void;
}

const props = defineProps<Props>();
const emit = defineEmits<Emits>();

const gameData = injectGameData();

const dialog = ref(false);
const selectedFactory = ref<GameItem | null>(null);
const searchQuery = ref('');

const factories = computed(() => {
    return gameData.gameFactoriesArray.filter(item =>
        item.recipeDictionary && !item.isAbstractClassItem
    );
});

const recipes = computed(() => {
    if (!selectedFactory.value) return [];
    const recipeDictionary = gameData.getItemRecipeDictionary(selectedFactory.value);
    let allRecipes = Array.from(recipeDictionary.recipesMap.values());

    // Filter by search query
    if (searchQuery.value.trim()) {
        const query = searchQuery.value.toLowerCase();
        allRecipes = allRecipes.filter(recipe =>
            (recipe.name && recipe.name.toLowerCase().includes(query)) ||
            (recipe.longName && recipe.longName.toLowerCase().includes(query))
        );
    }

    return allRecipes;
});

watch(() => props.modelValue, (newVal) => {
    dialog.value = newVal;
    if (newVal) {
        // Reset on open
        selectedFactory.value = null;
        searchQuery.value = '';
    }
});

watch(dialog, (newVal) => {
    emit('update:modelValue', newVal);
});

function selectRecipe(recipe: GameRecipe) {
    if (!selectedFactory.value?.recipeDictionary?.name) return;
    emit('copy', recipe, selectedFactory.value.recipeDictionary.name);
    dialog.value = false;
}

function getFactoryIcon(factory: GameItem): string {
    // Try to use the factory's image name as icon
    return factory.name || 'mdi-factory';
}

function getRecipeTimeDisplay(recipe: GameRecipe): string {
    // Display time in seconds
    return `${recipe.time.toFixed(2)}`;
}
</script>

<template>
    <v-dialog v-model="dialog" max-width="900px">
        <v-card>
            <v-card-title>
                <span></span>
                <v-spacer />
                <v-btn :icon="mdiClose" variant="text" @click="dialog = false" />
            </v-card-title>

            <v-card-text>
                <!-- Factory Selection -->
                <v-select
                    v-model="selectedFactory"
                    :items="factories"
                    item-title="label"
                    return-object
                    label="/"
                    :prepend-icon="mdiFactory"
                    clearable
                >
                    <template #item="{item, props}">
                        <v-list-item v-bind="props">
                            <template #prepend>
                                <icon-component :image="item.raw.name" />
                            </template>
                            <v-list-item-title>{{ item.raw.label }}</v-list-item-title>
                            <v-list-item-subtitle v-if="item.raw.longName && item.raw.longName !== item.raw.label">
                                {{ item.raw.longName }}
                            </v-list-item-subtitle>
                        </v-list-item>
                    </template>
                    <template #selection="{item}">
                        <div class="d-flex align-center">
                            <icon-component :image="item.raw.name" class="mr-2" />
                            {{ item.raw.label }}
                        </div>
                    </template>
                </v-select>

                <!-- Recipe List -->
                <div v-if="selectedFactory">
                    <v-text-field
                        v-model="searchQuery"
                        :prepend-icon="mdiFilter"
                        label=""
                        clearable
                        class="mb-2"
                        density="compact"
                    />

                    <v-list density="compact" max-height="400px" class="overflow-y-auto">
                        <v-list-item
                            v-for="recipe in recipes"
                            :key="recipe.name"
                            @click="selectRecipe(recipe)"
                            class="cursor-pointer"
                            hover
                        >
                            <template #prepend>
                                <icon-component :image="selectedFactory.name" />
                            </template>
                            <v-list-item-title>
                                {{ recipe.longName || recipe.name }}
                            </v-list-item-title>
                            <v-list-item-subtitle>
                                {{ getRecipeTimeDisplay(recipe) }} |
                                : {{ recipe.input.length }}  |
                                : {{ recipe.output.length }} 
                            </v-list-item-subtitle>
                            <template #append>
                                <v-btn
                                    size="small"
                                    :icon="mdiContentCopy"
                                    variant="text"
                                    @click.stop="selectRecipe(recipe)"
                                />
                            </template>
                        </v-list-item>
                        <v-list-item v-if="recipes.length === 0">
                            <v-list-item-title class="text-grey text-center">
                                {{ searchQuery ? '' : '' }}
                            </v-list-item-title>
                        </v-list-item>
                    </v-list>
                </div>

                <v-alert v-else type="info" variant="tonal" class="mt-4">
                    /
                </v-alert>
            </v-card-text>

            <v-card-actions>
                <v-spacer />
                <v-btn color="grey" variant="text" @click="dialog = false">
                    
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<style scoped>
.cursor-pointer {
    cursor: pointer;
}
</style>
