import type {GameRecipe, GameRecipeIO} from '#types/game-data';
import type {GameRecipeSerialized} from '#types/game-data-serialized';
import type {CustomRecipe, CustomRecipeIO} from './custom-recipe-service';
import {GameItemType} from '#types/constants';
import {type GameItemExData, GameItemExType} from '../../../data/coi/types/custom-game-data';

export interface InjectedRecipe {
    customRecipe: CustomRecipe;
    gameRecipe: GameRecipe;
}

/**
 * Inject custom recipes into a recipe dictionary
 * This modifies the recipe dictionary to include custom recipes
 */
export function injectCustomRecipes(
    recipeDictionary: any,
    customRecipes: CustomRecipe[],
    getGameItem: (name: string) => any
): InjectedRecipe[] {
    const injected: InjectedRecipe[] = [];

    if (!customRecipes || customRecipes.length === 0) {
        return injected;
    }

    // Unfreeze the recipe dictionary if it's frozen
    const isFrozen = Object.isFrozen(recipeDictionary);
    if (isFrozen) {
        // We need to work with unfrozen data
        // This is a limitation - we may need to modify the parser to not freeze
        throw new Error('Cannot inject recipes into frozen data structure');
    }

    for (const customRecipe of customRecipes) {
        try {
            const gameRecipe = createGameRecipeFromCustom(customRecipe, recipeDictionary, getGameItem);

            // Add to recipes array
            if (!recipeDictionary.recipes) {
                recipeDictionary.recipes = [];
            }
            recipeDictionary.recipes.push(gameRecipe);

            // Update recipesMap
            if (!recipeDictionary.recipesMap) {
                recipeDictionary.recipesMap = new Map();
            }
            recipeDictionary.recipesMap.set(gameRecipe.name, gameRecipe);

            // Update recipesByInputMap and recipesByOutputMap
            updateRecipesMap(recipeDictionary, gameRecipe, 'input');
            updateRecipesMap(recipeDictionary, gameRecipe, 'output');

            injected.push({
                customRecipe,
                gameRecipe
            });
        } catch (error) {
            console.error(`Failed to inject custom recipe ${customRecipe.name}:`, error);
        }
    }

    return injected;
}

/**
 * Create a GameRecipe from a CustomRecipe
 */
function createGameRecipeFromCustom(
    customRecipe: CustomRecipe,
    recipeDictionary: any,
    getGameItem: (name: string) => any
): GameRecipe {
    const input = customRecipe.input.map(io => createGameRecipeIO(io, true, recipeDictionary, getGameItem));
    const output = customRecipe.output.map(io => createGameRecipeIO(io, false, recipeDictionary, getGameItem));

    const recipeSerialized: GameRecipeSerialized = {
        name: customRecipe.name,
        longName: customRecipe.longName,
        input: input.map(io => ({
            name: io.name,
            count: io.count,
            flags: io.flags
        })),
        output: output.map(io => ({
            name: io.name,
            count: io.count,
            flags: io.flags
        })),
        time: customRecipe.time
    };

    // Create a minimal GameRecipe object
    const gameRecipe: any = {
        ...recipeSerialized,
        recipeDictionary: recipeDictionary,
        input,
        output
    };

    return gameRecipe;
}

/**
 * Create a GameRecipeIO from a CustomRecipeIO
 */
function createGameRecipeIO(
    customIO: CustomRecipeIO,
    isInput: boolean,
    recipeDictionary: any,
    getGameItem: (name: string) => any
): GameRecipeIO {
    const product = getGameItem(customIO.name);

    const gameIO: any = {
        name: customIO.name,
        count: customIO.count,
        flags: customIO.flags || 0,
        isInput,
        recipe: recipeDictionary,
        product,
        getCountPerSecond(item: any): number {
            const productExdata = this.product.exdata as GameItemExData;
            // Maintenance depot OUTPUT scales with time (uses time conversion)
            // Maintenance depot INPUT (maintenance kit): no time conversion (returns count directly)
            const isMaintenanceDepot = productExdata?.exType === GameItemExType.Maintenance && !this.isInput;
            const isPollution = productExdata?.exType === GameItemExType.Pollution;

            // Special items (electricity, workers, etc.) return count directly, no time conversion
            // maintenance depot outputs at timed rate, but inputs is always listed as per-minute
            // pollution also scales per time
            if((this.product.type === GameItemType.Special) && !isMaintenanceDepot && !isPollution) {
                return this.count;
            }
            // Game time is in ticks (1 tick = 1/60 second), so we multiply by 60 to get per-second rate
            return this.count * 60 / recipeDictionary.time;
        }
    };

    return gameIO;
}

/**
 * Update recipesByInputMap or recipesByOutputMap
 */
function updateRecipesMap(recipeDictionary: any, recipe: GameRecipe, type: 'input' | 'output'): void {
    const mapKey = type === 'input' ? 'recipesByInputMap' : 'recipesByOutputMap';
    const map = recipeDictionary[mapKey];

    if (!map) return;

    for (const io of recipe[type]) {
        let recipeNames = map.get(io.name);
        if (!recipeNames) {
            recipeNames = [];
            map.set(io.name, recipeNames);
        }
        recipeNames.push(recipe.name);
    }
}

/**
 * Validate a custom recipe before injection
 */
export function validateCustomRecipe(recipe: CustomRecipe, getGameItem: (name: string) => any): {valid: boolean, errors: string[]} {
    const errors: string[] = [];

    if (!recipe.name || recipe.name.trim().length === 0) {
        errors.push('');
    }

    if (!recipe.recipeDictionary || recipe.recipeDictionary.trim().length === 0) {
        errors.push('');
    }

    if (recipe.time <= 0) {
        errors.push('0');
    }

    for (const io of recipe.input) {
        const item = getGameItem(io.name);
        if (!item) {
            errors.push(` "${io.name}" `);
        }
        if (io.count <= 0) {
            errors.push(` "${io.name}" 0`);
        }
    }

    for (const io of recipe.output) {
        const item = getGameItem(io.name);
        if (!item) {
            errors.push(` "${io.name}" `);
        }
        if (io.count <= 0) {
            errors.push(` "${io.name}" 0`);
        }
    }

    if (recipe.output.length === 0) {
        errors.push('');
    }

    return {
        valid: errors.length === 0,
        errors
    };
}