import {createApp} from 'vue';
import {createVuetify} from 'vuetify';
import {aliases, mdi} from 'vuetify/iconsets/mdi-svg';
import App from './app.vue';

import './assets/vuetify.scss';
import './assets/main.scss';

// Register custom recipe components
import CustomRecipeDialog from './components/custom-recipe-dialog.vue';
import CustomRecipePanel from './components/custom-recipe-panel.vue';
import CustomRecipeExportDialog from './components/custom-recipe-export-dialog.vue';
import CustomRecipeImportDialog from './components/custom-recipe-import-dialog.vue';
import CopyRecipeDialog from './components/copy-recipe-dialog.vue';
import UnifiedRecipeEditor from './components/unified-recipe-editor.vue';

const app = createApp(App);

app.use(createVuetify({
    icons: {
        defaultSet: 'mdi',
        aliases,
        sets: {
            mdi,
        },
    },
}));

app.component('custom-recipe-dialog', CustomRecipeDialog);
app.component('custom-recipe-panel', CustomRecipePanel);
app.component('custom-recipe-export-dialog', CustomRecipeExportDialog);
app.component('custom-recipe-import-dialog', CustomRecipeImportDialog);
app.component('copy-recipe-dialog', CopyRecipeDialog);
app.component('unified-recipe-editor', UnifiedRecipeEditor);

app.mount('#app');
