Author: Alexey Usov (dax@xdax.ru, https://github.com/doubleaxe)
Debug Log Panel - Floating window for displaying debug logs
-->
<script setup lang="ts">
import {computed, ref} from 'vue';
import {mdiBug, mdiClose, mdiDeleteSweep, mdiDownload, mdiChevronDown, mdiChevronRight, mdiInformation, mdiAlert, mdiAlertCircle, mdiContentCopy, mdiDatabaseRemove} from '@mdi/js';
import {useDebugLog, type DebugLogEntry} from '@/scripts/debug-log-service';
import {useWindowDrag} from '@/composables/use-window-drag';

// Call composables directly in synchronous phase (no try-catch)
const debugLog = useDebugLog();
const {style: dragStyle, dragStart, isDragging, movableElem} = useWindowDrag('debug-log-panel');

const filterCategory = ref<'all' | DebugLogEntry['category']>('all');
const searchQuery = ref('');
const expandedEntries = ref<Set<string>>(new Set());

const filteredEntries = computed(() => {
    let entries = debugLog.entries;

    if (filterCategory.value !== 'all') {
        entries = entries.filter((e: DebugLogEntry) => e.category === filterCategory.value);
    }

    if (searchQuery.value) {
        const query = searchQuery.value.toLowerCase();
        entries = entries.filter((e: DebugLogEntry) =>
            e.message.toLowerCase().includes(query) ||
            e.action.toLowerCase().includes(query) ||
            JSON.stringify(e.data || {}).toLowerCase().includes(query)
        );
    }

    return entries;
});

function getSeverityIcon(severity: DebugLogEntry['severity']): string {
    switch (severity) {
        case 'info': return mdiInformation;
        case 'warn': return mdiAlert;
        case 'error': return mdiAlertCircle;
        default: return mdiInformation;
    }
}

function getSeverityColor(severity: DebugLogEntry['severity']): string {
    switch (severity) {
        case 'info': return 'info';
        case 'warn': return 'warning';
        case 'error': return 'error';
        default: return 'info';
    }
}

function formatTime(date: Date): string {
    const hours = date.getHours().toString().padStart(2, '0');
    const minutes = date.getMinutes().toString().padStart(2, '0');
    const seconds = date.getSeconds().toString().padStart(2, '0');
    const ms = date.getMilliseconds().toString().padStart(3, '0');
    return `${hours}:${minutes}:${seconds}.${ms}`;
}

function toggleExpand(id: string) {
    if (expandedEntries.value.has(id)) {
        expandedEntries.value.delete(id);
    } else {
        expandedEntries.value.add(id);
    }
    // Force reactivity
    expandedEntries.value = new Set(expandedEntries.value);
}

function clearLogs() {
    debugLog.clear();
    expandedEntries.value.clear();
}

function exportLogs() {
    const json = debugLog.exportToString();
    const blob = new Blob([json], {type: 'application/json'});
    const url = URL.createObjectURL(blob);
    const a = document.createElement('a');
    a.href = url;
    a.download = `debug-log-${Date.now()}.json`;
    document.body.appendChild(a);
    a.click();
    document.body.removeChild(a);
    URL.revokeObjectURL(url);
}

function clearLocalStorage() {
    //  localStorage
    localStorage.clear();

    // : category, action, message, data?
    debugLog.log('system', 'clearLocalStorage', ' localStorage ');
}

function formatEntryForCopy(entry: DebugLogEntry): string {
    const lines = [
        `[${entry.category.toUpperCase()}] ${entry.action}`,
        `: ${formatTime(entry.timestamp)}`,
        `: ${entry.severity.toUpperCase()}`,
        `: ${entry.message}`,
    ];

    if (entry.duration !== undefined) {
        lines.push(`: ${entry.duration.toFixed(2)}ms`);
    }

    if (entry.data) {
        lines.push(`\n:\n${JSON.stringify(entry.data, null, 2)}`);
    }

    if (entry.callStack.length > 0) {
        lines.push(`\n:`);
        entry.callStack.forEach((line, index) => {
            lines.push(`  ${index + 1}. ${line}`);
        });
    }

    return lines.join('\n');
}

async function copyEntry(entry: DebugLogEntry, event: Event) {
    event.stopPropagation(); // Prevent expanding the entry
    try {
        await navigator.clipboard.writeText(formatEntryForCopy(entry));
    } catch (error) {
        console.error('Failed to copy log entry:', error);
    }
}
</script>

<template>
    <v-card
        ref="movableElem"
        class="debug-log-panel"
        :style="dragStyle"
        width="600"
        elevation="8"
    >
        <!-- Draggable handle / Header -->
        <v-card-title
            class="drag-handle pa-3"
            @pointerdown="dragStart($event, null)"
        >
            <v-icon :icon="mdiBug" class="mr-2" size="small" />
            <span class="text-subtitle-2"></span>
            <v-spacer />
            <v-chip size="x-small" class="mr-2">{{ filteredEntries.length }} </v-chip>
            <v-btn :icon="mdiClose" size="x-small" variant="text" @click="debugLog.enabled = false" />
        </v-card-title>

        <v-divider />

        <!-- Toolbar -->
        <v-toolbar density="compact" color="surface">
            <v-select
                v-model="filterCategory"
                :items="[
                    {title: '', value: 'all'},
                    {title: '', value: 'custom-recipe'},
                    {title: 'UI ', value: 'ui'},
                    {title: '', value: 'system'},
                ]"
                density="compact"
                variant="outlined"
                hide-details
                :menu-props="{zIndex: 10000}"
                style="max-width: 140px"
            >
                <template #prepend-inner>
                    <v-icon size="x-small">mdi-filter</v-icon>
                </template>
            </v-select>

            <v-text-field
                v-model="searchQuery"
                placeholder="..."
                density="compact"
                variant="outlined"
                hide-details
                prepend-inner-icon="mdi-magnify"
                clearable
                class="ml-2"
                style="max-width: 200px"
            />

            <v-spacer />

            <v-tooltip location="bottom">
                <template #activator="{props}">
                    <v-btn v-bind="props" :icon="mdiDeleteSweep" size="small" variant="text" @click="clearLogs" />
                </template>
                <span></span>
            </v-tooltip>

            <v-tooltip location="bottom">
                <template #activator="{props}">
                    <v-btn v-bind="props" :icon="mdiDownload" size="small" variant="text" @click="exportLogs" />
                </template>
                <span></span>
            </v-tooltip>

            <v-tooltip location="bottom">
                <template #activator="{props}">
                    <v-btn v-bind="props" :icon="mdiDatabaseRemove" size="small" variant="text" color="warning" @click="clearLocalStorage" />
                </template>
                <span> ()</span>
            </v-tooltip>
        </v-toolbar>

        <v-divider />

        <!-- Log list -->
        <v-card-text class="pa-0 log-list-container">
            <div v-if="filteredEntries.length === 0" class="text-center pa-4 text-caption text-medium-emphasis">
                
            </div>
            <v-list v-else density="compact" class="log-list">
                <v-list-item
                    v-for="entry in filteredEntries"
                    :key="entry.id"
                    :class="['log-entry', `log-${entry.severity}`]"
                    @click="toggleExpand(entry.id)"
                >
                    <template #prepend>
                        <v-icon
                            :icon="getSeverityIcon(entry.severity)"
                            :color="getSeverityColor(entry.severity)"
                            size="small"
                            class="mr-2"
                        />
                    </template>

                    <v-list-item-title class="d-flex align-center">
                        <span class="text-caption font-mono">{{ formatTime(entry.timestamp) }}</span>
                        <span class="ml-2 text-body-2">{{ entry.action }}</span>
                        <v-chip size="x-small" class="ml-2" variant="outlined">{{ entry.category }}</v-chip>
                        <span v-if="entry.duration !== undefined" class="ml-2 text-caption text-medium-emphasis">
                            {{ entry.duration.toFixed(2) }}ms
                        </span>
                    </v-list-item-title>

                    <v-list-item-subtitle class="text-caption text-medium-emphasis mt-1">
                        {{ entry.message }}
                    </v-list-item-subtitle>

                    <template #append>
                        <v-tooltip location="left">
                            <template #activator="{props}">
                                <v-btn
                                    v-bind="props"
                                    :icon="mdiContentCopy"
                                    size="x-small"
                                    variant="text"
                                    density="compact"
                                    class="mr-1"
                                    @click="copyEntry(entry, $event)"
                                />
                            </template>
                            <span></span>
                        </v-tooltip>
                        <v-icon
                            :icon="expandedEntries.has(entry.id) ? mdiChevronDown : mdiChevronRight"
                            size="small"
                        />
                    </template>

                    <!-- Expanded details -->
                    <div v-if="expandedEntries.has(entry.id)" class="log-details mt-2">
                        <v-divider class="mb-2" />

                        <!-- Data section -->
                        <div v-if="entry.data" class="log-data">
                            <div class="text-subtitle-2 mb-1">:</div>
                            <pre class="log-data-pre">{{ JSON.stringify(entry.data, null, 2) }}</pre>
                        </div>

                        <!-- Call stack section -->
                        <div v-if="entry.callStack.length > 0" class="log-callstack mt-2">
                            <div class="text-subtitle-2 mb-1">:</div>
                            <div class="log-callstack-list">
                                <code
                                    v-for="(line, index) in entry.callStack"
                                    :key="index"
                                    class="log-callstack-line"
                                >{{ line }}</code>
                            </div>
                        </div>
                    </div>
                </v-list-item>
            </v-list>
        </v-card-text>
    </v-card>
</template>

<style scoped>
.debug-log-panel {
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.3) !important;
}

.drag-handle {
    cursor: grab;
    user-select: none;
    background-color: rgb(var(--v-theme-surface-variant));
}

.drag-handle:active {
    cursor: grabbing;
}

.log-list-container {
    max-height: 400px;
    overflow-y: auto;
}

.log-list {
    background-color: transparent;
}

.log-entry {
    border-bottom: 1px solid rgba(var(--v-border-color), 0.5);
    cursor: pointer;
    transition: background-color 0.2s;
}

.log-entry:hover {
    background-color: rgba(var(--v-theme-surface-variant), 0.3);
}

.log-info {
    border-left: 3px solid rgb(var(--v-theme-info));
}

.log-warn {
    border-left: 3px solid rgb(var(--v-theme-warning));
}

.log-error {
    border-left: 3px solid rgb(var(--v-theme-error));
    background-color: rgba(var(--v-theme-error), 0.1);
}

.log-details {
    background-color: rgba(var(--v-theme-surface-variant), 0.3);
    border-radius: 4px;
    padding: 8px;
}

.log-data-pre {
    background-color: rgba(0, 0, 0, 0.05);
    padding: 8px;
    border-radius: 4px;
    font-size: 11px;
    max-height: 200px;
    overflow: auto;
    font-family: 'Consolas', 'Monaco', 'Courier New', monospace;
}

.log-callstack-list {
    max-height: 150px;
