Author: Alexey Usov (dax@xdax.ru, https://github.com/doubleaxe)
Custom Recipe Export Dialog - Export custom recipes to JSON
-->
<script setup lang="ts">
import type {CustomRecipe} from '@/scripts/custom-recipe-service';
import {customRecipeService} from '@/scripts/custom-recipe-service';
import {useDebugLog} from '@/scripts/debug-log-service';
import {mdiContentCopy, mdiDownload, mdiClose} from '@mdi/js';
import {ref, computed, nextTick, onUnmounted} from 'vue';
import {useClipboard} from '@vueuse/core';

interface Props {
    modelValue: boolean;
}

interface Emits {
    (e: 'update:modelValue', value: boolean): void;
}

const props = defineProps<Props>();
const emit = defineEmits<Emits>();

const debugLog = useDebugLog();

const dialog = computed({
    get: () => props.modelValue,
    set: (value: boolean) => emit('update:modelValue', value),
});

const exportAllRecipes = ref(true);
const selectedExportRecipes = ref<CustomRecipe[]>([]);
const exportFileName = ref('custom-recipes');
const objectUrl = ref<string | null>(null);
const objectAnchor = ref<HTMLAnchorElement | null>(null);

const allRecipes = computed(() => customRecipeService.getAllRecipes());

const {copy, copied, isSupported: isClipboardSupported} = useClipboard();

function onExportAllChange() {
    if (exportAllRecipes.value) {
        selectedExportRecipes.value = [...allRecipes.value];
    } else {
        selectedExportRecipes.value = [];
    }
}

function getExportedJson(): string {
    const recipeNames = exportAllRecipes.value
        ? undefined
        : selectedExportRecipes.value.map(r => r.name);
    return customRecipeService.exportToString({recipeNames});
}

function copyToClipboard() {
    const startTime = performance.now();
    try {
        const recipeCount = exportAllRecipes.value ? allRecipes.value.length : selectedExportRecipes.value.length;
        copy(getExportedJson());
        const duration = performance.now() - startTime;
        debugLog.log('ui', 'export-copy', 'Exported to clipboard', {
            recipeCount,
            duration,
        });
    } catch (error) {
        const duration = performance.now() - startTime;
        debugLog.error('ui', 'export-copy', 'Failed to export to clipboard', {
            error: error instanceof Error ? error.message : String(error),
            duration,
        });
        console.error('Failed to copy to clipboard:', error);
    }
}

function downloadFile() {
    const startTime = performance.now();
    if(objectUrl.value) {
        URL.revokeObjectURL(objectUrl.value);
        objectUrl.value = null;
    }
    nextTick(() => {
        try {
            const json = getExportedJson();
            const blob = new Blob([json], {type: 'application/json'});
            const fileName = exportFileName.value.trim() || 'custom-recipes';
            objectUrl.value = URL.createObjectURL(blob);
            nextTick(() => {
                const anchor = objectAnchor.value;
                if (anchor) {
                    anchor.download = `${fileName}.custom-recipes.json`;
                    anchor.href = objectUrl.value!;
                    anchor.click();
                    const duration = performance.now() - startTime;
                    debugLog.log('ui', 'export-download', 'Exported to file', {
                        fileName,
                        duration,
                    });
                }
            });
        } catch (error) {
            const duration = performance.now() - startTime;
            debugLog.error('ui', 'export-download', 'Failed to export to file', {
                error: error instanceof Error ? error.message : String(error),
                duration,
            });
        }
    });
}

onUnmounted(() => {
    if(objectUrl.value) {
        URL.revokeObjectURL(objectUrl.value);
        objectUrl.value = null;
    }
});
</script>

<template>
    <v-dialog v-model="dialog" max-width="600px">
        <v-card>
            <v-card-title>
                <span></span>
                <v-spacer />
                <v-btn :icon="mdiClose" variant="text" @click="dialog = false" />
            </v-card-title>

            <v-card-text>
                <v-checkbox-btn
                    v-model="exportAllRecipes"
                    label=""
                    @update:model-value="onExportAllChange"
                />

                <v-select
                    v-if="!exportAllRecipes"
                    v-model="selectedExportRecipes"
                    :items="allRecipes"
                    item-title="longName"
                    item-value="name"
                    label=""
                    multiple
                    chips
                    return-object
                    class="mt-2"
                >
                    <template #item="{item, props}">
                        <v-list-item v-bind="props" :title="item.raw.longName || item.raw.name" />
                    </template>
                    <template #selection="{item}">
                        <v-chip size="small">
                            {{ item.raw.longName || item.raw.name }}
                        </v-chip>
                    </template>
                </v-select>

                <v-text-field
                    v-model="exportFileName"
                    label=""
                    prepend-inner-icon="mdi-file"
                    class="mt-4"
                />
            </v-card-text>

            <v-card-actions>
                <v-spacer />
                <v-btn
                    v-if="isClipboardSupported"
                    color="primary"
                    variant="outlined"
                    :prepend-icon="mdiContentCopy"
                    @click="copyToClipboard"
                >
                    {{ copied ? '' : '' }}
                </v-btn>
                <v-btn
                    color="primary"
                    variant="elevated"
                    :prepend-icon="mdiDownload"
                    :disabled="exportAllRecipes ? allRecipes.length === 0 : selectedExportRecipes.length === 0"
                    @click="downloadFile"
                >
                    
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <a
        v-if="objectUrl"
        ref="objectAnchor"
        class="d-none"
    />
</template>