import {inject, ref, shallowRef, unref, watch, type InjectionKey, type Ref, type ShallowRef} from 'vue';
import {useGameDataHolder, type GameData} from './game-data-holder';
export type {GameData};
import gameList from '#types/game-list.json';
import {createCss, loadScript} from '../load-script-css';
import type {GameImplementation} from '#types/game-implementation';
import {useDebounceFn} from '@vueuse/core';
import {useDynamicGameDataHolder, type DynamicGameData} from './dynamic-game-data-holder';
import {customRecipeService} from '../custom-recipe-service';
import {builtinExtensionService} from '../builtin-extension-service';
import {mergeExtensionItems, mergeExtensionImages} from '../extension-data-injector';
import {inflate} from 'pako';
import {toUint8Array} from 'js-base64';
import type {GameDataSerialized} from '#types/game-data-serialized';

const __VERSION__ = 'v=' + encodeURIComponent(import.meta.env.VITE_VERSION);

function initializeCss(gameId: string) {
    const cssContent = `.icon-component {background-image: url(games/${encodeURIComponent(gameId)}/images.png?${__VERSION__});}`;
    createCss(cssContent);
}

export const GameDataKey = Symbol('GameData') as InjectionKey<ShallowRef<DynamicGameData>>;

export function useGameDataProvider(gameId: Ref<string>, onError?: (e: unknown) => void) {
    const isLoading = ref(false);
    const isReady = ref(false);
    const isFailed = ref(false);
    const isAutomatic = ref(false);
    const gameDataRef = shallowRef<DynamicGameData | undefined>();

    function loadGameData() {
        const _gameId = unref(gameId);
        Promise.resolve()
            .then(() => new Promise((resolve) => { setTimeout(resolve, 1); }))
            .then(async () => {
                if(!(_gameId in gameList))
                    throw new Error(`Invalid game id: ${_gameId}`);

                // Load game implementation script
                const _gameImplementation = await loadScript(`games/${encodeURIComponent(_gameId)}/game.js?${__VERSION__}`, {
                    globalVariable: 'gameImplementation',
                }) as GameImplementation;

                // Check if Bricks extension should be enabled (read from localStorage)
                const settingsKey = `settings-${_gameId}`;
                const storedSettings = localStorage.getItem(settingsKey);
                let enableBricksExtension = true; // Default enabled
                if (storedSettings) {
                    try {
                        const parsed = JSON.parse(storedSettings);
                        enableBricksExtension = parsed.enableBricksExtension !== false;
                    } catch (e) {
                        console.warn('Failed to parse settings, using defaults');
                    }
                }

                // Load extensions if enabled
                if (enableBricksExtension && _gameId === 'coi') {
                    await builtinExtensionService.loadExtensions(_gameId, ['bricks']);
                    console.log('[GameData] Bricks extension enabled');
                } else {
                    await builtinExtensionService.loadExtensions(_gameId, []);
                    console.log('[GameData] Bricks extension disabled');
                }

                // Get base game data
                const baseGameDataRaw = _gameImplementation.useGameData();
                let gameDataSerialized: GameDataSerialized;

                // Decompress if needed and merge extension data
                if (typeof baseGameDataRaw === 'string') {
                    // Decompress the game data
                    const compressed = toUint8Array(baseGameDataRaw);
                    gameDataSerialized = JSON.parse(inflate(compressed, {to: 'string'}));
                } else {
                    gameDataSerialized = baseGameDataRaw;
                }

                // Merge extension items into game data
                const extensionData = builtinExtensionService.getMergedExtensionData();
                if (extensionData.items.length > 0) {
                    gameDataSerialized = mergeExtensionItems(gameDataSerialized, extensionData);
                    console.log(`[GameData] Merged ${extensionData.items.length} extension items`);
                }

                // Create modified game implementation with merged data
                const modifiedGameImplementation: GameImplementation = {
                    ..._gameImplementation,
                    useGameData: () => gameDataSerialized,
                };

                const baseGameData = useGameDataHolder(modifiedGameImplementation);

                // Create dynamic game data holder with custom recipe support
                const dynamicGameData = useDynamicGameDataHolder(baseGameData);

                // Load custom recipes
                const customRecipes = customRecipeService.getAllRecipes();
                if (customRecipes.length > 0) {
                    try {
                        dynamicGameData.injectCustomRecipes(customRecipes);
                    } catch (error) {
                        console.error('Failed to load custom recipes:', error);
                    }
                }

                // Setup extension image support
                const extensionImages = builtinExtensionService.getExtensionImages();

                // Add extension image support methods for icon-component
                (dynamicGameData as any).isExtensionImage = (itemName: string) => {
                    return builtinExtensionService.isItemFromExtension(itemName);
                };

                (dynamicGameData as any).getExtensionImageUrl = () => {
                    const extId = Object.keys(extensionImages)[0]; // Get first extension id (e.g., 'bricks')
                    if (extId) {
                        return builtinExtensionService.getExtensionImageUrl(extId, _gameId);
                    }
                    return undefined;
                };

                // Override getImage to support extension image coordinates
                if (Object.keys(extensionImages).length > 0) {
                    const originalGetImage = dynamicGameData.getImage.bind(dynamicGameData);

                    (dynamicGameData as any).getImage = (name: string) => {
                        // Check if item is from extension
                        for (const [extId, extImageData] of Object.entries(extensionImages)) {
                            if (extImageData[name]) {
                                return extImageData[name];
                            }
                        }
                        // Fall back to original game images
                        return originalGetImage(name);
                    };
                    console.log('[GameData] Extension image support enabled');
                }

                initializeCss(_gameId);

                gameDataRef.value = dynamicGameData;
                isLoading.value = false;
                isReady.value = true;
            })
            .catch((err) => {
                isLoading.value = false;
                isFailed.value = true;
                onError?.(err);
            });
    }
    const loadGameDataDebounce = useDebounceFn(loadGameData, 250);

    watch(gameId, () => {
        if(!unref(gameId)) {
            return;
        }
        gameDataRef.value = undefined;
        isLoading.value = true;
        isReady.value = false;
        isFailed.value = false;
        unref(isAutomatic) ? loadGameData() : loadGameDataDebounce();
    }, {immediate: true});

    return {
        isLoading,
        isReady,
        isFailed,
        isAutomatic,
        gameDataRef,
        gameList,
    };
}

export function injectGameData(): DynamicGameData {
    const gameData = unref(inject(GameDataKey));
    if(!gameData)
        throw new Error('gameData not instantiated');
    return gameData;
}
