Author: Alexey Usov (dax@xdax.ru, https://github.com/doubleaxe)
Custom Recipe Dialog - Add/Edit Custom Recipes
-->
<script setup lang="ts">
import type {GameItem, GameRecipeDictionary} from '#types/game-data';
import type {CustomRecipe, CustomRecipeIO} from '@/scripts/custom-recipe-service';
import type {GameRecipeIOFlags} from '#types/constants';
import {injectGameData} from '@/scripts/data';
import {customRecipeService} from '@/scripts/custom-recipe-service';
import {validateCustomRecipe} from '@/scripts/custom-recipe-injector';
import {useDebugLog} from '@/scripts/debug-log-service';
import {injectSettings} from '@/scripts/settings';
import {mdiClose, mdiPlus, mdiTrashCan, mdiPencil, mdiEyeOffOutline, mdiChevronUp, mdiPercent} from '@mdi/js';
import {computed, ref, watch, unref} from 'vue';
import {useDebounceFn} from '@vueuse/core';

interface Props {
    modelValue: boolean;
    editRecipe?: CustomRecipe;
    initialEditItem?: { type: 'input' | 'output'; index: number } | null;
}

interface Emits {
    (e: 'update:modelValue', value: boolean): void;
    (e: 'saved', recipe: CustomRecipe): void;
}

const props = defineProps<Props>();
const emit = defineEmits<Emits>();

const gameData = injectGameData();
const settings = injectSettings();
const debugLog = useDebugLog();

const dialog = ref(false);
const form = ref(false);
const loading = ref(false);
const errors = ref<string[]>([]);

// Form fields
const recipeName = ref('');
const recipeLongName = ref('');
const selectedFactory = ref<GameRecipeDictionary | null>(null);
const recipeTime = ref(10);

const inputItems = ref<CustomRecipeIO[]>([]);
const outputItems = ref<CustomRecipeIO[]>([]);

// Dialog for adding items
const itemDialog = ref(false);
const itemDialogType = ref<'input' | 'output'>('input');
const selectedItem = ref<GameItem | null>(null);
const itemQuantity = ref(1);

// State for editing existing items
const editingItemIndex = ref<number | null>(null);
const editingItemType = ref<'input' | 'output'>('input');

// Item search state
const itemSearch = ref('');
const filteredItems = ref<GameItem[]>([]);
const pageSize = 10;
const page = ref(1);

const availableFactories = computed(() => gameData.gameFactoriesArray.filter(item =>
    item.recipeDictionary && !item.isAbstractClassItem
));

const availableItems = computed(() => {
    let items = gameData.gameItemsArray.filter(item =>
        !item.isAbstractClassItem
    );

    // Filter buildings if setting is enabled
    if (settings.filterBuildingsInCustomRecipe) {
        items = items.filter(item => !item.recipeDictionary);
    }

    return items;
});

// Pagination and search
const pages = computed(() => (Math.ceil(unref(filteredItems).length / pageSize) || 1));
const currentPage = computed(() => {
    const start = (unref(page) - 1) * pageSize;
    return unref(filteredItems).slice(start, start + pageSize);
});

const updateSearch = useDebounceFn(() => {
    let val = unref(itemSearch);
    val = val && val.trim();
    if(!val) {
        filteredItems.value = availableItems.value;
        page.value = 1;
        return;
    }
    const searchText = val.toLowerCase().split(/\s+/).map((s) => s.trim());
    filteredItems.value = availableItems.value.filter((item) => (
        searchText.every((l) => !l || (item.lowerLabel.indexOf(l) > -1)))
    );
    if(unref(page) > unref(pages)) {
        page.value = unref(pages);
    }
}, 200, {maxWait: 500});

watch(itemSearch, (value, oldValue) => {
    if(value != oldValue)
        updateSearch();
});

// Initialize filtered items
watch(availableItems, (items) => {
    filteredItems.value = items;
}, {immediate: true});

// Special item types
const specialItemTypes = [
    {label: '', value: 1, icon: 'lightning'},
    {label: '', value: 2, icon: 'cog'},
    {label: '', value: 3, icon: 'chip'},
    {label: '', value: 4, icon: 'flask'},
    {label: '', value: 5, icon: 'wrench'},
    {label: '', value: 6, icon: 'cloud'},
    {label: '', value: 7, icon: 'account'},
];

// Item dialog - special parameters
const specialItemType = ref<number | null>(null);
const flagHasProbability = ref(false);
const flagHideInMenu = ref(false);
const flagHideOnWindow = ref(false);
const flagRoundToCeil = ref(false);

const showAdvancedOptions = ref(false);

watch(() => props.modelValue, (newVal) => {
    dialog.value = newVal;
    if (newVal) {
        if (props.editRecipe) {
            loadRecipe(props.editRecipe);
        } else {
            resetForm();
        }
    }
});

watch(dialog, (newVal) => {
    emit('update:modelValue', newVal);
});

// Watch for initialEditItem changes to auto-open item edit dialog
watch(() => props.initialEditItem, (newVal) => {
    if (newVal && dialog.value) {
        // Use nextTick to ensure recipe data is loaded first
        setTimeout(() => {
            editItem(newVal.type, newVal.index);
        }, 50);
    }
}, { immediate: true });

function loadRecipe(recipe: CustomRecipe) {
    recipeName.value = recipe.name;
    recipeLongName.value = recipe.longName || '';
    const factory = gameData.getGameItem(recipe.recipeDictionary);
    selectedFactory.value = factory?.recipeDictionary || null;
    recipeTime.value = recipe.time;
    inputItems.value = [...recipe.input];
    outputItems.value = [...recipe.output];
}

function resetForm() {
    recipeName.value = '';
    recipeLongName.value = '';
    selectedFactory.value = null;
    recipeTime.value = 10;
    inputItems.value = [];
    outputItems.value = [];
    errors.value = [];
}

function openAddItem(type: 'input' | 'output') {
    itemDialogType.value = type;
    editingItemIndex.value = null;  // Reset editing state
    editingItemType.value = 'input';
    selectedItem.value = null;
    itemQuantity.value = 1;
    resetSpecialParameters();
    itemDialog.value = true;
}

function editItem(type: 'input' | 'output', index: number) {
    const items = type === 'input' ? inputItems.value : outputItems.value;
    const item = items[index];

    editingItemType.value = type;
    editingItemIndex.value = index;
    itemDialogType.value = type;

    // Pre-fill the dialog with existing item data
    selectedItem.value = gameData.getGameItem(item.name) || null;
    itemQuantity.value = item.count;

    // Set flags
    const flags = item.flags || 0;
    flagHasProbability.value = !!(flags & 1);
    flagHideInMenu.value = !!(flags & 4);
    flagHideOnWindow.value = !!(flags & 8);
    flagRoundToCeil.value = !!(flags & 16);

    // Determine special item type from item name
    specialItemType.value = getSpecialItemTypeFromName(item.name);

    itemDialog.value = true;
}

function resetSpecialParameters() {
    specialItemType.value = null;
    flagHasProbability.value = false;
    flagHideInMenu.value = false;
    flagHideOnWindow.value = false;
    flagRoundToCeil.value = false;
    showAdvancedOptions.value = false;
}

function onSpecialItemTypeChange() {
    // Auto-set recommended flags based on special item type
    const type = specialItemType.value;
    if (type === null) {
        // Reset all flags when no special type selected
        flagHasProbability.value = false;
        flagHideInMenu.value = false;
        flagHideOnWindow.value = false;
        flagRoundToCeil.value = false;
        return;
    }

    // Reset first
    flagHasProbability.value = false;
    flagHideInMenu.value = false;
    flagHideOnWindow.value = false;
    flagRoundToCeil.value = false;

    // Set recommended flags
    if (type === 1 || type === 2 || type === 3) {
        // Electricity, MechPower, Computing: hide in menu and window
        flagHideInMenu.value = true;
        flagHideOnWindow.value = true;
    } else if (type === 5) {
        // Maintenance: hide in menu and window
        flagHideInMenu.value = true;
        flagHideOnWindow.value = true;
    } else if (type === 7) {
        // Worker: hide + round to ceil
        flagHideInMenu.value = true;
        flagHideOnWindow.value = true;
        flagRoundToCeil.value = true;
    }
}

function getFlags(): number {
    let flags = 0;
    if (flagHasProbability.value) flags |= 1;  // HasProbability
    if (flagHideInMenu.value) flags |= 4;      // HideInMenu
    if (flagHideOnWindow.value) flags |= 8;    // HideOnWindow
    if (flagRoundToCeil.value) flags |= 16;    // RoundToCeil
    return flags;
}

function getFlagLabel(flags: number): string[] {
    const labels: string[] = [];
    if (flags & 1) labels.push('');
    if (flags & 4) labels.push('');
    if (flags & 8) labels.push('');
    if (flags & 16) labels.push('');
    return labels;
}

function getSpecialItemIcon(type: number): string {
    const icons: Record<number, string> = {
        1: 'mdi-lightning-bolt',      // Electricity
        2: 'mdi-cog',                 // MechPower
        3: 'mdi-chip',                // Computing
        4: 'mdi-flask',               // Research
        5: 'mdi-wrench',              // Maintenance
        6: 'mdi-cloud',               // Pollution
        7: 'mdi-account-hard-hat',    // Worker
    };
    return icons[type] || 'mdi-help';
}

function getSpecialItemTypeFromName(name: string): number | null {
    // Map special item names to their types
    const specialItemMap: Record<string, number> = {
        'Electricity': 1,
        'MechPower': 2,
        'Computing': 3,
        'Research': 4,
        'Maintenance': 5,
        'Pollution': 6,
        'Worker': 7,
    };
    return specialItemMap[name] || null;
}

function addCustomItem() {
    if (!selectedItem.value) return;

    const io: CustomRecipeIO = {
        name: selectedItem.value.name,
        count: itemQuantity.value,
        flags: getFlags()
    };

    // Check if we're editing an existing item
    if (editingItemIndex.value !== null && editingItemType.value === itemDialogType.value) {
        // Update existing item
        if (itemDialogType.value === 'input') {
            inputItems.value[editingItemIndex.value] = io;
        } else {
            outputItems.value[editingItemIndex.value] = io;
        }
    } else {
        // Add new item
        if (itemDialogType.value === 'input') {
            inputItems.value.push(io);
        } else {
            outputItems.value.push(io);
        }
    }

    // Reset editing state
    editingItemIndex.value = null;
    editingItemType.value = 'input';
    itemDialog.value = false;
}

function removeItem(type: 'input' | 'output', index: number) {
    if (type === 'input') {
        inputItems.value.splice(index, 1);
    } else {
        outputItems.value.splice(index, 1);
    }
}

function saveRecipe() {
    const startTime = performance.now();
    errors.value = [];

    debugLog.log('ui', 'save-recipe', `Attempting to save recipe: ${recipeName.value}`, {
        isEdit: !!props.editRecipe,
        recipeName: recipeName.value,
        inputCount: inputItems.value.length,
        outputCount: outputItems.value.length,
    });

    if (!selectedFactory.value) {
        errors.value.push('/');
        debugLog.warn('ui', 'save-recipe', 'Save failed: No factory selected');
        return;
    }

    const recipe: CustomRecipe = {
        name: recipeName.value,
        longName: recipeLongName.value || undefined,
        input: inputItems.value,
        output: outputItems.value,
        time: recipeTime.value,
        recipeDictionary: selectedFactory.value.name
    };

    // Validate
    const validation = validateCustomRecipe(recipe, gameData.getGameItem.bind(gameData));
    if (!validation.valid) {
        errors.value = validation.errors;
        debugLog.warn('ui', 'save-recipe', 'Validation failed', {errors: validation.errors});
        return;
    }

    loading.value = true;

    setTimeout(() => {
        try {
            // Check if this is an existing custom recipe or a new one
            const existingRecipe = customRecipeService.getRecipe(recipe.name);
            const isUpdate = props.editRecipe && existingRecipe;

            if (isUpdate) {
                customRecipeService.updateRecipe(recipe);
            } else {
                customRecipeService.addRecipe(recipe);
            }

            const duration = performance.now() - startTime;
            debugLog.log('ui', 'save-recipe', `Recipe saved successfully: ${recipe.name}`, {
                isUpdate,
                duration,
            });

            emit('saved', recipe);
            dialog.value = false;
        } catch (error) {
            const duration = performance.now() - startTime;
            const errorMsg = '' + (error instanceof Error ? error.message : String(error));
            errors.value = [errorMsg];
            debugLog.error('ui', 'save-recipe', 'Save failed', {
                error: error instanceof Error ? error.message : String(error),
                duration,
            });
        } finally {
            loading.value = false;
        }
    }, 100);
}

const canSave = computed(() => {
    return form.value &&
        recipeName.value.trim().length > 0 &&
        selectedFactory.value !== null &&
        recipeTime.value > 0 &&
        inputItems.value.length > 0 &&
        outputItems.value.length > 0;
});
</script>

<template>
    <v-dialog v-model="dialog" max-width="800px" persistent>
        <v-card>
            <v-card-title>
                <span>{{ editRecipe ? '' : '' }}</span>
                <v-spacer />
                <v-btn :icon="mdiClose" variant="text" @click="dialog = false" />
            </v-card-title>

            <v-card-text>
                <v-form ref="form" v-model="form">
                    <v-row>
                        <v-col cols="12">
                            <v-text-field
                                v-model="recipeName"
                                label=""
                                :rules="[(v: string) => !!v || '']"
                                required
                                :disabled="!!editRecipe"
                            />
                        </v-col>
                    </v-row>

                    <v-row>
                        <v-col cols="12">
                            <v-text-field
                                v-model="recipeLongName"
                                label=""
                                clearable
                            />
                        </v-col>
                    </v-row>

                    <v-row>
                        <v-col cols="12">
                            <v-select
                                v-model="selectedFactory"
                                :items="availableFactories"
                                item-title="label"
                                item-value="recipeDictionary"
                                label="/"
                                return-object
                                :rules="[(v: any) => !!v || '/']"
                                required
                            >
                                <template #item="{item, props}">
                                    <v-list-item v-bind="props">
                                        <v-list-item-title>{{ item.raw.label }}</v-list-item-title>
                                        <v-list-item-subtitle>{{ item.raw.recipeDictionary?.longName || item.raw.name }}</v-list-item-subtitle>
                                    </v-list-item>
                                </template>
                            </v-select>
                        </v-col>
                    </v-row>

                    <v-row>
                        <v-col cols="6">
                            <v-text-field
                                v-model.number="recipeTime"
                                type="number"
                                label=""
                                :rules="[(v: number) => v > 0 || '0']"
                                required
                            />
                        </v-col>
                    </v-row>

                    <!-- Input Items -->
                    <v-row>
                        <v-col cols="12">
                            <div class="d-flex align-center mb-2">
                                <span class="text-h6"></span>
                                <v-spacer />
                                <v-btn
                                    color="primary"
                                    size="small"
                                    :prepend-icon="mdiPlus"
                                    @click="openAddItem('input')"
                                >
                                    
                                </v-btn>
                            </div>

                            <v-list density="compact">
                                <v-list-item
                                    v-for="(item, index) in inputItems"
                                    :key="`input-${index}`"
                                >
                                    <template #prepend>
                                        <icon-component
                                            :image="item.name"
                                        />
                                    </template>
                                    <v-list-item-title>
                                        {{ gameData.getGameItem(item.name)?.label }}
                                    </v-list-item-title>
                                    <v-list-item-subtitle>
                                        : {{ item.count }}
                                        <template v-if="item.flags">
                                            <v-chip
                                                v-for="(label, i) in getFlagLabel(item.flags)"
                                                :key="i"
                                                size="x-small"
                                                class="ml-1"
                                            >
                                                {{ label }}
                                            </v-chip>
                                        </template>
                                    </v-list-item-subtitle>
                                    <template #append>
                                        <v-btn
                                            size="small"
                                            :icon="mdiPencil"
                                            variant="text"
                                            @click="editItem('input', index)"
                                        />
                                        <v-btn
                                            size="small"
                                            :icon="mdiTrashCan"
                                            color="error"
                                            variant="text"
                                            @click="removeItem('input', index)"
                                        />
                                    </template>
                                </v-list-item>
                                <v-list-item v-if="inputItems.length === 0">
                                    <v-list-item-title class="text-grey text-center">
                                        
                                    </v-list-item-title>
                                </v-list-item>
                            </v-list>
                        </v-col>
                    </v-row>

                    <!-- Output Items -->
                    <v-row>
                        <v-col cols="12">
                            <div class="d-flex align-center mb-2">
                                <span class="text-h6"></span>
                                <v-spacer />
                                <v-btn
                                    color="primary"
                                    size="small"
                                    :prepend-icon="mdiPlus"
                                    @click="openAddItem('output')"
                                >
                                    
                                </v-btn>
                            </div>

                            <v-list density="compact">
                                <v-list-item
                                    v-for="(item, index) in outputItems"
                                    :key="`output-${index}`"
                                >
                                    <template #prepend>
                                        <icon-component
                                            :image="item.name"
                                        />
                                    </template>
                                    <v-list-item-title>
                                        {{ gameData.getGameItem(item.name)?.label }}
                                    </v-list-item-title>
                                    <v-list-item-subtitle>
                                        : {{ item.count }}
                                        <template v-if="item.flags">
                                            <v-chip
                                                v-for="(label, i) in getFlagLabel(item.flags)"
                                                :key="i"
                                                size="x-small"
                                                class="ml-1"
                                            >
                                                {{ label }}
                                            </v-chip>
                                        </template>
                                    </v-list-item-subtitle>
                                    <template #append>
                                        <v-btn
                                            size="small"
                                            :icon="mdiPencil"
                                            variant="text"
                                            @click="editItem('output', index)"
                                        />
                                        <v-btn
                                            size="small"
                                            :icon="mdiTrashCan"
                                            color="error"
                                            variant="text"
                                            @click="removeItem('output', index)"
                                        />
                                    </template>
                                </v-list-item>
                                <v-list-item v-if="outputItems.length === 0">
                                    <v-list-item-title class="text-grey text-center">
                                        
                                    </v-list-item-title>
                                </v-list-item>
                            </v-list>
                        </v-col>
                    </v-row>

                    <!-- Errors -->
                    <v-alert v-if="errors.length > 0" type="error" class="mt-4">
                        <div v-for="(error, index) in errors" :key="index">
                            {{ error }}
                        </div>
                    </v-alert>
                </v-form>
            </v-card-text>

            <v-card-actions>
                <v-spacer />
                <v-btn color="grey" variant="text" @click="dialog = false">
                    
                </v-btn>
                <v-btn
                    color="primary"
                    variant="elevated"
                    :loading="loading"
                    :disabled="!canSave"
                    @click="saveRecipe"
                >
                    
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <!-- Add Item Dialog -->
    <v-dialog v-model="itemDialog" max-width="600px">
        <v-card>
            <v-card-title>
                {{ editingItemIndex !== null ? '' : '' }}{{ itemDialogType === 'input' ? '' : '' }}
            </v-card-title>
            <v-card-text>
                <v-autocomplete
                    v-model="selectedItem"
                    v-model:search="itemSearch"
                    :items="currentPage"
                    item-title="label"
                    return-object
                    clearable
                    no-filter
                    hide-details
                    label=""
                    :rules="[(v: any) => !!v || '']"
                    required
                    @focus="(page = 1)"
                >
                    <template #item="{item, props}">
                        <v-list-item v-bind="props">
                            <template #prepend>
                                <icon-component
                                    :image="item.raw.name"
                                />
                            </template>
                            <v-list-item-title>{{ item.raw.label }}</v-list-item-title>
                            <v-list-item-subtitle>{{ item.raw.name }}</v-list-item-subtitle>
                        </v-list-item>
                    </template>
                    <template #selection="{item}">
                        <div class="d-flex align-center">
                            <icon-component
                                :image="item.raw.name"
                                class="mr-2"
                            />
                            {{ item.raw.label }}
                        </div>
                    </template>
                    <template #append-item>
                        <v-pagination v-model="page" density="compact" :length="pages" :total-visible="5" />
                    </template>
                </v-autocomplete>

                <v-text-field
                    v-model.number="itemQuantity"
                    type="number"
                    label=""
                    :rules="[(v: number) => v > 0 || '0']"
                    required
                />

                <v-select
                    v-model="specialItemType"
                    :items="specialItemTypes"
                    item-title="label"
                    item-value="value"
                    label=""
                    clearable
                    @update:modelValue="onSpecialItemTypeChange"
                >
                    <template #selection="{item}">
                        <div v-if="item" class="d-flex align-center">
                            <v-icon class="mr-2">{{ getSpecialItemIcon(item.value) }}</v-icon>
                            {{ item.label }}
                        </div>
                    </template>
                    <template #item="{item, props}">
                        <v-list-item v-bind="props">
                            <template #prepend>
                                <v-icon>{{ getSpecialItemIcon(item.value) }}</v-icon>
                            </template>
                            <v-list-item-title>{{ item.label }}</v-list-item-title>
                        </v-list-item>
                    </template>
                </v-select>

                <v-expansion-panels v-if="specialItemType" class="mt-2">
                    <v-expansion-panel>
                        <v-expansion-panel-title>
                            <span class="text-body-2"></span>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <v-checkbox-btn
                                v-model="flagHasProbability"
                                label=""
                                density="compact"
                                class="mt-2"
                            >
                                <template #prepend>
                                    <v-icon :icon="mdiPercent" class="mr-2" />
                                </template>
                            </v-checkbox-btn>
                            <v-checkbox-btn
                                v-model="flagHideInMenu"
                                label=""
                                density="compact"
                            >
                                <template #prepend>
                                    <v-icon :icon="mdiEyeOffOutline" class="mr-2" />
                                </template>
                            </v-checkbox-btn>
                            <v-checkbox-btn
                                v-model="flagHideOnWindow"
                                label=""
                                density="compact"
                            >
                                <template #prepend>
                                    <v-icon :icon="mdiEyeOffOutline" class="mr-2" />
                                </template>
                            </v-checkbox-btn>
                            <v-checkbox-btn
                                v-model="flagRoundToCeil"
                                label=""
                                density="compact"
                            >
                                <template #prepend>
                                    <v-icon :icon="mdiChevronUp" class="mr-2" />
                                </template>
                            </v-checkbox-btn>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                </v-expansion-panels>
            </v-card-text>
            <v-card-actions>
                <v-spacer />
                <v-btn color="grey" variant="text" @click="itemDialog = false">
                    
                </v-btn>
                <v-btn
                    color="primary"
                    variant="elevated"
                    :disabled="!selectedItem || itemQuantity <= 0"
                    @click="addCustomItem"
                >
                    {{ editingItemIndex !== null ? '' : '' }}
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>

<style scoped>
.text-h6 {
    font-size: 1rem;
    font-weight: 500;
}
</style>