/*
Recipe Pack Service - Manage recipe pack files with metadata
Handles loading, saving, and managing recipe packs like myrecipe.json
*/

import {debugLogService} from './debug-log-service';
import {customRecipeService} from './custom-recipe-service';
import type {CustomRecipe} from './custom-recipe-service';

// Valid log categories
type LogCategory = 'custom-recipe' | 'ui' | 'system';

export interface RecipePack {
    Version: number;
    PackName: string;
    Author?: string;
    Description?: string;
    Recipes: CustomRecipe[];
}

export interface RecipePackRecipe {
    Id: string;
    Name: string;
    MachineId: string;
    DurationSeconds: number;
    IsEnabled: boolean;
    PowerMultiplierPercent: number;
    DisableConversionLoss: boolean;
    MinUtilizationPercent: number;
    DestroyReason: string;
    Inputs: RecipePackIO[];
    Outputs: RecipePackIO[];
}

export interface RecipePackIO {
    ProductId: string;
    Quantity: number;
    PortSelector: string;
    TriggerAtStart: boolean;
    HideInUi: boolean;
    Flags?: number;  //  flags 
}

// Convert between CustomRecipe and RecipePackRecipe formats
export function customRecipeToPackRecipe(recipe: CustomRecipe, machineId: string): RecipePackRecipe {
    return {
        Id: recipe.name,
        Name: recipe.longName || recipe.name,
        MachineId: machineId,
        DurationSeconds: recipe.time,
        IsEnabled: true,
        PowerMultiplierPercent: 100,
        DisableConversionLoss: false,
        MinUtilizationPercent: 100,
        DestroyReason: 'General',
        Inputs: recipe.input.map(io => ({
            ProductId: io.name,
            Quantity: io.count,
            PortSelector: '*',
            TriggerAtStart: false,
            HideInUi: !!((io.flags || 0) & 12),  // HideInMenu | HideOnWindow
            Flags: io.flags ?? 0,  //  flags
        })),
        Outputs: recipe.output.map(io => ({
            ProductId: io.name,
            Quantity: io.count,
            PortSelector: '*',
            TriggerAtStart: false,
            HideInUi: !!((io.flags || 0) & 12),
            Flags: io.flags ?? 0,  //  flags
        })),
    };
}

export function packRecipeToCustomRecipe(packRecipe: RecipePackRecipe): CustomRecipe {
    return {
        name: packRecipe.Id,
        longName: packRecipe.Name,
        input: packRecipe.Inputs.map(io => ({
            name: io.ProductId,
            count: io.Quantity,
            flags: (io as any).Flags ?? 0,  //  flags 0
        })),
        output: packRecipe.Outputs.map(io => ({
            name: io.ProductId,
            count: io.Quantity,
            flags: (io as any).Flags ?? 0,  //  flags 0
        })),
        time: packRecipe.DurationSeconds,
        recipeDictionary: packRecipe.MachineId,
    };
}

const PACK_STORAGE_KEY = 'coi-recipe-packs';
const ACTIVE_PACK_KEY = 'coi-active-pack';
const DEFAULT_PACK_VERSION = 1;

export class RecipePackService {
    private activePack: RecipePack | null = null;
    private packList: RecipePack[] = [];
    private listeners: Set<() => void> = new Set();

    constructor() {
        this.loadPackList();
        this.restoreActivePack();
    }

    // Load the list of packs from localStorage
    private loadPackList(): void {
        try {
            const data = localStorage.getItem(PACK_STORAGE_KEY);
            if (data) {
                this.packList = JSON.parse(data);
            }
        } catch (error) {
            console.error('Failed to load pack list:', error);
            this.packList = [];
        }
    }

    // Save the pack list to localStorage
    private savePackList(): void {
        try {
            localStorage.setItem(PACK_STORAGE_KEY, JSON.stringify(this.packList));
        } catch (error) {
            console.error('Failed to save pack list:', error);
        }
    }

    // Create a new recipe pack
    createNewPack(packName: string): RecipePack {
        const newPack: RecipePack = {
            Version: DEFAULT_PACK_VERSION,
            PackName: packName,
            Author: '',
            Description: '',
            Recipes: [],
        };
        return newPack;
    }

    // Load a pack from a File object
    async loadFromFile(file: File): Promise<RecipePack> {
        return new Promise((resolve, reject) => {
            const reader = new FileReader();
            reader.onload = (e) => {
                try {
                    const content = e.target?.result as string;
                    const pack = JSON.parse(content) as RecipePack;

                    // Validate pack structure
                    if (!pack.Version || !pack.PackName || !Array.isArray(pack.Recipes)) {
                        throw new Error('Invalid pack format');
                    }

                    // Convert pack recipes to custom recipes format if needed
                    if (pack.Recipes.length > 0) {
                        const firstRecipe = pack.Recipes[0] as any;
                        if ('MachineId' in firstRecipe && 'Inputs' in firstRecipe) {
                            // This is in the pack recipe format, convert to custom recipe
                            pack.Recipes = (pack.Recipes as unknown as RecipePackRecipe[])
                                .map(packRecipeToCustomRecipe);
                        }
                    }

                    debugLogService.log('ui', 'load-file', `Loaded pack: ${pack.PackName}`, {
                        recipeCount: pack.Recipes.length,
                    });
                    resolve(pack);
                } catch (error) {
                    debugLogService.error('ui', 'load-file', 'Failed to load pack', {
                        error: error instanceof Error ? error.message : String(error),
                    });
                    reject(error);
                }
            };
            reader.onerror = () => reject(new Error('Failed to read file'));
            reader.readAsText(file);
        });
    }

    // Save a pack to a file download
    saveToFile(pack: RecipePack, filename: string): void {
        try {
            // Convert custom recipes to pack recipe format
            const packToSave = {
                Version: pack.Version,
                PackName: pack.PackName,
                Author: pack.Author,
                Description: pack.Description,
                Recipes: pack.Recipes.map(recipe =>
                    customRecipeToPackRecipe(recipe, recipe.recipeDictionary)
                ),
            };

            const json = JSON.stringify(packToSave, null, 2);
            const blob = new Blob([json], {type: 'application/json'});
            const url = URL.createObjectURL(blob);

            const a = document.createElement('a');
            a.href = url;
            a.download = filename.endsWith('.json') ? filename : `${filename}.json`;
            document.body.appendChild(a);
            a.click();
            document.body.removeChild(a);
            URL.revokeObjectURL(url);

            debugLogService.log('ui', 'save-file', `Saved pack: ${pack.PackName}`, {
                filename,
                recipeCount: pack.Recipes.length,
            });
        } catch (error) {
            debugLogService.error('ui', 'save-file', 'Failed to save pack', {
                error: error instanceof Error ? error.message : String(error),
            });
            throw error;
        }
    }

    // Sync all recipes from active pack to custom recipe service
    syncToCustomRecipeService(): void {
        if (!this.activePack) return;

        const packRecipeNames = new Set(this.activePack.Recipes.map(r => r.name));
        const currentCustomRecipes = customRecipeService.getAllRecipes();

        // Remove recipes that are in custom service but not in active pack
        for (const recipe of currentCustomRecipes) {
            if (!packRecipeNames.has(recipe.name)) {
                customRecipeService.deleteRecipe(recipe.name);
            }
        }

        // Add or update recipes from active pack
        for (const recipe of this.activePack.Recipes) {
            const existing = customRecipeService.getRecipe(recipe.name);
            if (existing) {
                customRecipeService.updateRecipe(recipe);
            } else {
                customRecipeService.addRecipe(recipe);
            }
        }

        debugLogService.log('ui', 'sync', `Synced ${this.activePack.Recipes.length} recipes to custom recipe service`);
    }

    // Remove all recipes from active pack from custom recipe service
    removeFromCustomRecipeService(): void {
        if (!this.activePack) return;

        for (const recipe of this.activePack.Recipes) {
            customRecipeService.deleteRecipe(recipe.name);
        }

        debugLogService.log('ui', 'sync', `Removed ${this.activePack.Recipes.length} recipes from custom recipe service`);
    }

    // Set the active pack
    setActivePack(pack: RecipePack | null): void {
        this.activePack = pack;
        if (pack) {
            this.syncToCustomRecipeService();
            //  localStorage
            localStorage.setItem(ACTIVE_PACK_KEY, pack.PackName);
            //  packList
            this.saveActivePackToStorage();
        } else {
            localStorage.removeItem(ACTIVE_PACK_KEY);
        }
        this.notify();
        debugLogService.log('ui', 'set-active', pack ? `Active pack: ${pack.PackName}` : 'No active pack');
    }

    // Get the active pack
    getActivePack(): RecipePack | null {
        return this.activePack;
    }

    // Update pack metadata
    updatePackMetadata(updates: Partial<Pick<RecipePack, 'PackName' | 'Author' | 'Description'>>): void {
        if (!this.activePack) {
            throw new Error('No active pack');
        }
        // Create new pack object to trigger Vue reactivity
        this.activePack = {
            ...this.activePack,
            ...updates
        };
        this.notify();

        //  localStorage
        this.saveActivePackToStorage();

        debugLogService.log('ui', 'update-metadata', 'Updated pack metadata', updates);
    }

    // Add a recipe to the active pack
    addRecipe(recipe: CustomRecipe): void {
        if (!this.activePack) {
            throw new Error('No active pack');
        }

        // Create new recipes array to trigger Vue reactivity
        const newRecipes = [...this.activePack.Recipes];
        const existingIndex = newRecipes.findIndex(r => r.name === recipe.name);

        if (existingIndex >= 0) {
            // Update existing
            newRecipes[existingIndex] = recipe;
            debugLogService.log('ui', 'add-recipe', `Updated recipe: ${recipe.name}`);
        } else {
            // Add new
            newRecipes.push(recipe);
            debugLogService.log('ui', 'add-recipe', `Added recipe: ${recipe.name}`);
        }

        // Create new pack object to trigger Vue reactivity
        this.activePack = {
            ...this.activePack,
            Recipes: newRecipes
        };
        this.notify();

        //  localStorage
        this.saveActivePackToStorage();
    }

    // Remove a recipe from the active pack
    removeRecipe(recipeName: string): void {
        if (!this.activePack) {
            throw new Error('No active pack');
        }

        const index = this.activePack.Recipes.findIndex(r => r.name === recipeName);
        if (index >= 0) {
            const newRecipes = this.activePack.Recipes.filter((_, i) => i !== index);
            // Create new pack object to trigger Vue reactivity
            this.activePack = {
                ...this.activePack,
                Recipes: newRecipes
            };
            this.notify();
            debugLogService.log('ui', 'remove-recipe', `Removed recipe: ${recipeName}`);

            //  localStorage
            this.saveActivePackToStorage();
        }
    }

    // Get all recipes from the active pack
    getRecipes(): CustomRecipe[] {
        return this.activePack?.Recipes ?? [];
    }

    // Get recipes for a specific machine
    getRecipesByMachine(machineId: string): CustomRecipe[] {
        return this.getRecipes().filter(r => r.recipeDictionary === machineId);
    }

    // Generate a unique recipe ID
    generateRecipeId(machineId: string): string {
        const timestamp = Date.now().toString(36);
        const random = Math.random().toString(36).substring(2, 10);
        return `CustomRecipe_${machineId}_${timestamp}${random}`;
    }

    // Save the active pack to localStorage
    saveActivePackToStorage(): void {
        if (!this.activePack) {
            throw new Error('No active pack');
        }

        // Update or add to pack list
        const existingIndex = this.packList.findIndex(p => p.PackName === this.activePack!.PackName);
        if (existingIndex >= 0) {
            this.packList[existingIndex] = {...this.activePack};
        } else {
            this.packList.push({...this.activePack});
        }

        this.savePackList();
        debugLogService.log('ui', 'save-storage', `Saved pack to storage: ${this.activePack.PackName}`);
    }

    // Load a pack from localStorage by name
    loadPackFromStorage(packName: string): RecipePack | null {
        const pack = this.packList.find(p => p.PackName === packName);
        if (pack) {
            this.activePack = {...pack, Recipes: [...pack.Recipes]};
            this.notify();
            debugLogService.log('ui', 'load-storage', `Loaded pack from storage: ${packName}`);
            return this.activePack;
        }
        return null;
    }

    // Get list of all pack names
    getPackList(): string[] {
        return this.packList.map(p => p.PackName);
    }

    // Delete a pack from storage
    deletePackFromStorage(packName: string): void {
        const index = this.packList.findIndex(p => p.PackName === packName);
        if (index >= 0) {
            this.packList.splice(index, 1);
            this.savePackList();
            if (this.activePack?.PackName === packName) {
                this.activePack = null;
                localStorage.removeItem(ACTIVE_PACK_KEY);
                this.notify();
            }
            debugLogService.log('ui', 'delete', `Deleted pack: ${packName}`);
        }
    }

    // Restore the active pack from localStorage
    private restoreActivePack(): void {
        const activePackName = localStorage.getItem(ACTIVE_PACK_KEY);
        if (activePackName) {
            const pack = this.loadPackFromStorage(activePackName);
            if (pack) {
                //  customRecipeService
                this.syncToCustomRecipeService();
                debugLogService.log('ui', 'restore', `Restored active pack: ${activePackName}`);
            }
        }
    }

    // Subscribe to changes
    subscribe(listener: () => void): () => void {
        this.listeners.add(listener);
        return () => {
            this.listeners.delete(listener);
        };
    }

    private notify(): void {
        this.listeners.forEach(listener => listener());
    }
}

export const recipePackService = new RecipePackService();
