<!--
Author: Alexey Usov (dax@xdax.ru, https://github.com/doubleaxe)
Custom Recipe Import Dialog - Import custom recipes from JSON
-->
<script setup lang="ts">
import type {CustomRecipeExportData, CustomRecipeImportResult} from '@/scripts/custom-recipe-service';
import {customRecipeService} from '@/scripts/custom-recipe-service';
import {injectGameData} from '@/scripts/data';
import {useDebugLog} from '@/scripts/debug-log-service';
import {mdiContentPaste, mdiFileUploadOutline, mdiClose} from '@mdi/js';
import {ref, computed, nextTick} from 'vue';

interface Props {
    modelValue: boolean;
}

interface Emits {
    (e: 'update:modelValue', value: boolean): void;
    (e: 'imported'): void;
}

const props = defineProps<Props>();
const emit = defineEmits<Emits>();

const gameData = injectGameData();
const debugLog = useDebugLog();

const dialog = computed({
    get: () => props.modelValue,
    set: (value: boolean) => {
        if (!value) {
            closeImportDialog();
        }
        emit('update:modelValue', value);
    },
});

const importTab = ref<'paste' | 'file'>('paste');
const importText = ref('');
const importFileName = ref('');
const fileInput = ref<HTMLInputElement | null>(null);
const showInputFile = ref(false);
const importPreview = ref<CustomRecipeExportData | null>(null);
const importResult = ref<CustomRecipeImportResult | null>(null);
const importDuplicateOption = ref<'overwrite' | 'skip' | 'rename'>('skip');
const importing = ref(false);

const isClipboardSupported = computed(() => 'clipboard' in navigator);

const canImport = computed(() => {
    return importPreview.value !== null;
});

function closeImportDialog() {
    importText.value = '';
    importFileName.value = '';
    importPreview.value = null;
    importResult.value = null;
    showInputFile.value = false;
}

async function pasteFromClipboard() {
    try {
        const text = await navigator.clipboard.readText();
        importText.value = text;
        parseImportData(text);
        debugLog.log('ui', 'import-paste', 'Pasted from clipboard', {textLength: text.length});
    } catch (error) {
        debugLog.error('ui', 'import-paste', 'Failed to paste from clipboard', {
            error: error instanceof Error ? error.message : String(error),
        });
        console.error('Failed to paste from clipboard:', error);
    }
}

function triggerFileInput() {
    showInputFile.value = false;
    nextTick(() => {
        showInputFile.value = true;
        nextTick(() => {
            fileInput.value?.click();
        });
    });
}

function handleFileSelect(event: Event) {
    const target = event.target as HTMLInputElement;
    const selectedFiles = target.files;
    if(!selectedFiles || !selectedFiles[0])
        return;

    const selectedFile = selectedFiles[0];
    importFileName.value = selectedFile.name;

    const reader = new FileReader();
    reader.onload = function() {
        const fileContents = reader.result;
        if(typeof(fileContents) != 'string') {
            console.error('Invalid file type');
            return;
        }
        importText.value = fileContents;
        parseImportData(fileContents);
    };
    reader.onerror = function() {
        console.error('Failed to read file:', reader.error);
    };
    reader.readAsText(selectedFile);
}

function parseImportData(text: string) {
    try {
        const data: CustomRecipeExportData = JSON.parse(text);
        importPreview.value = data;
        importResult.value = null;
        debugLog.log('ui', 'import-parse', 'Parsed import data', {
            recipeCount: data.recipes?.length || 0,
            version: data.version,
        });
    } catch (error) {
        debugLog.error('ui', 'import-parse', 'Failed to parse import data', {
            error: error instanceof Error ? error.message : String(error),
        });
        console.error('Failed to parse import data:', error);
        importPreview.value = null;
    }
}

async function startImport() {
    if (!importPreview.value || importing.value) return;

    debugLog.log('ui', 'import-start', 'Starting import', {
        recipeCount: importPreview.value.recipes.length,
        duplicateOption: importDuplicateOption.value,
    });

    importing.value = true;
    try {
        const result = await customRecipeService.importFromString(
            importText.value,
            {onDuplicate: importDuplicateOption.value},
            gameData.getGameItem.bind(gameData) // Pass the actual game item validation function
        );
        importResult.value = result;

        debugLog.log('ui', 'import-complete', 'Import completed', {
            success: result.success,
            imported: result.imported.length,
            skipped: result.skipped.length,
            overwritten: result.overwritten.length,
            renamed: result.renamed.length,
            invalid: result.invalid.length,
        });

        if (result.success && result.imported.length > 0) {
            emit('imported');
        }
    } catch (error) {
        debugLog.error('ui', 'import-complete', 'Import failed', {
            error: error instanceof Error ? error.message : String(error),
        });
        console.error('Import failed:', error);
        importResult.value = {
            success: false,
            imported: [],
            skipped: [],
            overwritten: [],
            renamed: [],
            invalid: [],
            errors: [{
                recipe: 'parsing',
                error: error instanceof Error ? error.message : String(error)
            }]
        };
    } finally {
        importing.value = false;
    }
}

function formatDate(isoString: string): string {
    const date = new Date(isoString);
    return date.toLocaleDateString() + ' ' + date.toLocaleTimeString();
}

function getImportSummary(): string {
    if (!importResult.value) return '';
    const result = importResult.value;
    const parts: string[] = [];
    if (result.imported.length > 0) parts.push(`: ${result.imported.length} `);
    if (result.skipped.length > 0) parts.push(`: ${result.skipped.length} `);
    if (result.overwritten.length > 0) parts.push(`: ${result.overwritten.length} `);
    if (result.renamed.length > 0) parts.push(`: ${result.renamed.length} `);
    if (result.invalid.length > 0) parts.push(`: ${result.invalid.length} `);
    return parts.join(', ');
}

function hasDuplicateRecipes(): boolean {
    if (!importPreview.value) return false;
    const existingRecipes = customRecipeService.getAllRecipes();
    const existingNames = new Set(existingRecipes.map(r => r.name));
    return importPreview.value.recipes.some(r => existingNames.has(r.name));
}
</script>

<template>
    <v-dialog v-model="dialog" max-width="800px">
        <v-card>
            <v-card-title>
                <span></span>
                <v-spacer />
                <v-btn :icon="mdiClose" variant="text" @click="closeImportDialog" />
            </v-card-title>

            <v-card-text>
                <v-tabs v-model="importTab" class="mb-4">
                    <v-tab value="paste"></v-tab>
                    <v-tab value="file"></v-tab>
                </v-tabs>

                <v-window v-model="importTab">
                    <v-window-item value="paste">
                        <v-textarea
                            v-model="importText"
                            label="JSON"
                            variant="outlined"
                            rows="6"
                        />
                        <v-btn
                            v-if="isClipboardSupported"
                            block
                            :prepend-icon="mdiContentPaste"
                            @click="pasteFromClipboard"
                        >
                            
                        </v-btn>
                    </v-window-item>

                    <v-window-item value="file">
                        <v-btn
                            block
                            :prepend-icon="mdiFileUploadOutline"
                            @click="triggerFileInput"
                        >
                            
                        </v-btn>
                        <input
                            v-if="showInputFile"
                            ref="fileInput"
                            type="file"
                            accept=".json,.custom-recipes.json,.cr.json"
                            class="d-none"
                            @change="handleFileSelect"
                        />
                        <div v-if="importFileName" class="mt-2 text-body-2">
                            : {{ importFileName }}
                        </div>
                    </v-window-item>
                </v-window>

                <v-alert v-if="importPreview" type="info" class="mt-4">
                    <div class="text-subtitle-2 mb-2"></div>
                    <div>: {{ importPreview.version }}</div>
                    <div>: {{ formatDate(importPreview.createdAt) }}</div>
                    <div>: {{ importPreview.recipes.length }}</div>
                    <div v-if="importPreview.gameId">
                        ID: {{ importPreview.gameId }}
                    </div>
                </v-alert>

                <v-select
                    v-if="importPreview && hasDuplicateRecipes()"
                    v-model="importDuplicateOption"
                    label=""
                    :items="[
                        {title: '', value: 'skip'},
                        {title: '', value: 'overwrite'},
                        {title: '', value: 'rename'},
                    ]"
                    class="mt-4"
                />

                <v-alert
                    v-if="importResult"
                    :type="importResult.success ? 'success' : 'error'"
                    class="mt-4"
                >
                    <div class="text-subtitle-2 mb-2"></div>
                    <div>{{ getImportSummary() }}</div>
                </v-alert>

                <v-alert v-if="importResult?.errors && importResult.errors.length > 0" type="error" class="mt-4">
                    <div class="text-subtitle-2 mb-2"></div>
                    <div v-for="(error, index) in importResult.errors" :key="index" class="text-caption">
                        {{ error.recipe }}: {{ error.error }}
                    </div>
                </v-alert>
            </v-card-text>

            <v-card-actions>
                <v-spacer />
                <v-btn color="grey" variant="text" @click="dialog = false">
                    
                </v-btn>
                <v-btn
                    v-if="importPreview && !importResult"
                    color="primary"
                    variant="elevated"
                    :disabled="!canImport"
                    :loading="importing"
                    @click="startImport"
                >
                    
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>
</template>