Custom Recipe Management Panel - List and Manage Custom Recipes
-->
<script setup lang="ts">
import type {CustomRecipe} from '@/scripts/custom-recipe-service';
import type {GameRecipe} from '#types/game-data';
import {customRecipeService} from '@/scripts/custom-recipe-service';
import {injectGameData} from '@/scripts/data';
import {mdiPencil, mdiTrashCan, mdiPlus, mdiExportVariant, mdiImport, mdiContentCopy} from '@mdi/js';
import {computed, ref, watch, onMounted} from 'vue';

interface Props {
    modelValue: boolean;
}

interface Emits {
    (e: 'update:modelValue', value: boolean): void;
    (e: 'recipes-changed'): void;
}

const props = defineProps<Props>();
const emit = defineEmits<Emits>();

const gameData = injectGameData();

const dialog = ref(false);
const editDialog = ref(false);
const exportDialog = ref(false);
const importDialog = ref(false);
const copyDialog = ref(false);
const loading = ref(false);
const currentEditRecipe = ref<CustomRecipe | undefined>(undefined);
const deleteConfirm = ref(false);
const recipeToDelete = ref<CustomRecipe | null>(null);

// State for editing a specific item within a recipe
const initialEditItem = ref<{ type: 'input' | 'output'; index: number } | null>(null);

const recipes = ref<CustomRecipe[]>([]);

watch(() => props.modelValue, (newVal: boolean) => {
    dialog.value = newVal;
});

watch(dialog, (newVal: boolean) => {
    emit('update:modelValue', newVal);
    if (newVal) {
        loadRecipes();
    }
});

// Subscribe to recipe service changes
onMounted(() => {
    const unsubscribe = customRecipeService.subscribe(() => {
        loadRecipes();
    });

    return unsubscribe;
});

function loadRecipes() {
    recipes.value = customRecipeService.getAllRecipes();
}

function openAddDialog() {
    currentEditRecipe.value = undefined;
    editDialog.value = true;
}

function openEditDialog(recipe: CustomRecipe) {
    currentEditRecipe.value = recipe;
    initialEditItem.value = null;  // Reset item edit state
    editDialog.value = true;
}

function openEditItemDialog(recipe: CustomRecipe, type: 'input' | 'output', index: number) {
    currentEditRecipe.value = recipe;
    initialEditItem.value = { type, index };
    editDialog.value = true;
}

function confirmDelete(recipe: CustomRecipe) {
    recipeToDelete.value = recipe;
    deleteConfirm.value = true;
}

function deleteRecipe() {
    if (!recipeToDelete.value) return;

    loading.value = true;
    setTimeout(() => {
        customRecipeService.deleteRecipe(recipeToDelete.value!.name);
        recipeToDelete.value = null;
        deleteConfirm.value = false;
        emit('recipes-changed');
        loading.value = false;
    }, 100);
}

function onRecipeSaved(recipe: CustomRecipe) {
    emit('recipes-changed');
    loadRecipes();
}

function onImported() {
    emit('recipes-changed');
    loadRecipes();
}

function openCopyDialog() {
    copyDialog.value = true;
}

function onCopyFromBuiltIn(builtInRecipe: GameRecipe, factoryName: string) {
    // Convert built-in recipe to custom recipe format
    const customRecipe = convertToCustomRecipe(builtInRecipe, factoryName);
    currentEditRecipe.value = customRecipe;
    editDialog.value = true;
}

function convertToCustomRecipe(recipe: GameRecipe, factoryName: string): CustomRecipe {
    // Use the recipe's name directly as the custom recipe name
    // This allows users to easily recognize and modify it
    const baseName = recipe.name;
    let uniqueName = baseName;
    let counter = 1;

    // Generate unique name if already exists
    while (customRecipeService.getRecipe(uniqueName)) {
        uniqueName = `${baseName}_${counter}`;
        counter++;
    }

    return {
        name: uniqueName,
        longName: (recipe.longName || recipe.name) + ' ()',
        input: recipe.input.map(io => ({
            name: io.name,
            count: io.count,
            flags: io.flags || 0,
        })),
        output: recipe.output.map(io => ({
            name: io.name,
            count: io.count,
            flags: io.flags || 0,
        })),
        time: recipe.time,
        recipeDictionary: factoryName,  // Use the factory's recipeDictionary name
    };
}

const groupedRecipes = computed(() => {
    const groups = new Map<string, CustomRecipe[]>();
    for (const recipe of recipes.value) {
        const dict = recipe.recipeDictionary;
        if (!groups.has(dict)) {
            groups.set(dict, []);
        }
        groups.get(dict)!.push(recipe);
    }
    return Array.from(groups.entries());
});

function getFactoryLabel(dictName: string): string {
    const factory = gameData.gameItemsArray.find(item =>
        item.recipeDictionary?.name === dictName
    );
    return factory?.label || dictName;
}
</script>

<template>
    <v-dialog v-model="dialog" max-width="900px">
        <v-card>
            <v-card-title>
                <div class="d-flex align-center justify-space-between w-100">
                    <span></span>
                    <div class="d-flex align-center">
                        <v-tooltip location="bottom">
                            <template #activator="{ props }">
                                <v-btn
                                    v-bind="props"
                                    size="small"
                                    :icon="mdiExportVariant"
                                    variant="text"
                                    color="primary"
                                    class="mr-1"
                                    @click="exportDialog = true"
                                />
                            </template>
                            <span></span>
                        </v-tooltip>
                        <v-tooltip location="bottom">
                            <template #activator="{ props }">
                                <v-btn
                                    v-bind="props"
                                    size="small"
                                    :icon="mdiImport"
                                    variant="text"
                                    color="primary"
                                    @click="importDialog = true"
                                />
                            </template>
                            <span></span>
                        </v-tooltip>
                    </div>
                </div>
            </v-card-title>

            <v-card-text>
                <div class="d-flex ga-2 mb-4">
                    <v-btn
                        color="primary"
                        :prepend-icon="mdiPlus"
                        @click="openAddDialog"
                    >
                        
                    </v-btn>
                    <v-btn
                        color="secondary"
                        :prepend-icon="mdiContentCopy"
                        variant="outlined"
                        @click="openCopyDialog"
                    >
                        
                    </v-btn>
                </div>

                <v-expansion-panels v-if="groupedRecipes.length > 0">
                    <v-expansion-panel
                        v-for="[dictName, dictRecipes] in groupedRecipes"
                        :key="dictName"
                    >
                        <v-expansion-panel-title>
                            <div class="d-flex align-center">
                                <span class="mr-2">{{ getFactoryLabel(dictName) }}</span>
                                <v-chip size="small" color="grey">
                                    {{ dictRecipes.length }}
                                </v-chip>
                            </div>
                        </v-expansion-panel-title>
                        <v-expansion-panel-text>
                            <v-list>
                                <v-list-item
                                    v-for="recipe in dictRecipes"
                                    :key="recipe.name"
                                    class="recipe-item"
                                >
                                    <template #prepend>
                                        <div class="recipe-info">
                                            <div class="recipe-name">{{ recipe.longName || recipe.name }}</div>
                                            <div class="recipe-time">: {{ recipe.time }}</div>
                                        </div>
                                    </template>
                                    <v-list-item-subtitle class="recipe-io">
                                        <div class="io-section">
                                            <span class="io-label">:</span>
                                            <v-chip
                                                v-for="(io, index) in recipe.input"
                                                :key="`in-${index}`"
                                                size="small"
                                                class="mr-1"
                                            >
                                                {{ gameData.getGameItem(io.name)?.label || io.name }} x{{ io.count }}
                                                <v-btn
                                                    size="x-small"
                                                    :icon="mdiPencil"
                                                    variant="text"
                                                    class="ml-1"
                                                    @click.stop="openEditItemDialog(recipe, 'input', index)"
                                                />
                                            </v-chip>
                                        </div>
                                        <div class="io-section mt-1">
                                            <span class="io-label">:</span>
                                            <v-chip
                                                v-for="(io, index) in recipe.output"
                                                :key="`out-${index}`"
                                                size="small"
                                                class="mr-1"
                                                color="success"
                                            >
                                                {{ gameData.getGameItem(io.name)?.label || io.name }} x{{ io.count }}
                                                <v-btn
                                                    size="x-small"
                                                    :icon="mdiPencil"
                                                    variant="text"
                                                    class="ml-1"
                                                    @click.stop="openEditItemDialog(recipe, 'output', index)"
                                                />
                                            </v-chip>
                                        </div>
                                    </v-list-item-subtitle>
                                    <template #append>
                                        <v-btn
                                            size="small"
                                            :icon="mdiPencil"
                                            variant="text"
                                            @click.stop="openEditDialog(recipe)"
                                        />
                                        <v-btn
                                            size="small"
                                            :icon="mdiTrashCan"
                                            color="error"
                                            variant="text"
                                            @click.stop="confirmDelete(recipe)"
                                        />
                                    </template>
                                </v-list-item>
                            </v-list>
                        </v-expansion-panel-text>
                    </v-expansion-panel>
                </v-expansion-panels>

                <v-alert v-if="recipes.length === 0" type="info" class="mt-4">
                    ""
                </v-alert>
            </v-card-text>

            <v-card-actions>
                <v-spacer />
                <v-btn color="grey" variant="text" @click="dialog = false">
                    
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <custom-recipe-dialog
        v-model="editDialog"
        :edit-recipe="currentEditRecipe"
        :initial-edit-item="initialEditItem"
        @saved="onRecipeSaved"
    />

    <!-- Delete Confirmation Dialog -->
    <v-dialog v-model="deleteConfirm" max-width="400px">
        <v-card>
            <v-card-title></v-card-title>
            <v-card-text>
                 "{{ recipeToDelete?.longName || recipeToDelete?.name }}" 
            </v-card-text>
            <v-card-actions>
                <v-spacer />
                <v-btn color="grey" variant="text" @click="deleteConfirm = false">
                    
                </v-btn>
                <v-btn
                    color="error"
                    variant="elevated"
                    :loading="loading"
                    @click="deleteRecipe"
                >
                    
                </v-btn>
            </v-card-actions>
        </v-card>
    </v-dialog>

    <custom-recipe-export-dialog v-model="exportDialog" />
    <custom-recipe-import-dialog v-model="importDialog" @imported="onImported" />
    <copy-recipe-dialog v-model="copyDialog" @copy="onCopyFromBuiltIn" />
</template>

<style scoped>
.recipe-info {
    min-width: 200px;
}

.recipe-name {
    font-weight: 500;
}

.recipe-time {
    font-size: 0.875rem;
    color: #757575;
}

.recipe-io {
    margin-left: 16px;
}

.io-section {
    display: flex;
    align-items: center;
    flex-wrap: wrap;
    gap: 4px;
}

.io-label {
    font-weight: 500;
    margin-right: 8px;
}

.recipe-item {
    border-bottom: 1px solid #e0e0e0;
}

.recipe-item:last-child {
    border-bottom: none;
}

.io-section .v-chip {
    display: inline-flex;
    align-items: center;
}

.io-section .v-chip .v-btn {
    margin-left: -4px;
    margin-right: -8px;
    opacity: 0.7;
}

.io-section .v-chip .v-btn:hover {
    opacity: 1;
}
</style>