<script setup lang="ts">
import {injectBlueprintModel} from '@/scripts/model/store';
import {mdiSigma, mdiHome, mdiBookEdit, mdiBookOpenPageVariant} from '@mdi/js';
import {injectGameData} from '@/scripts/data';
import {ref} from 'vue';
import {customRecipeService} from '@/scripts/custom-recipe-service';

const gameData = injectGameData();
const blueprintModel = injectBlueprintModel();
const homeReference = [location.protocol, '//', location.host, location.pathname].join('')
    + `?gameId=${gameData.gameDescription.name}`;

const customRecipePanelVisible = ref(false);
const unifiedRecipeEditorVisible = ref(false);

// Handle recipe changes
function onRecipesChanged() {
    // Refresh the dynamic game data with new custom recipes
    if ('injectCustomRecipes' in gameData) {
        const recipes = customRecipeService.getAllRecipes();
        (gameData as any).injectCustomRecipes(recipes);
    }
    // Force blueprint recalculation
    if (blueprintModel.autoSolveGraph) {
        blueprintModel.solveGraph(true);
    }
}
</script>

<template>
    <tooltip-button
        tooltip=""
        :icon="mdiSigma"
        :disabled="blueprintModel.autoSolveGraph"
        @click="blueprintModel.solveGraph(true)"
    />
    <v-switch v-model="blueprintModel.autoSolveGraph" label="" hide-details class="mr-1" color="primary" />
    <v-divider vertical />
    <a :href="homeReference" target="_blank" class="anchor-plain">
        <tooltip-button
            tooltip=""
            :icon="mdiHome"
        />
    </a>
    <button-load-share />
    <button-save-share />
    <button-generate-link />
    <v-divider vertical />
    <tooltip-button
        tooltip=""
        :icon="mdiBookEdit"
        @click="customRecipePanelVisible = true"
    />
    <tooltip-button
        tooltip=""
        :icon="mdiBookOpenPageVariant"
        color="primary"
        @click="unifiedRecipeEditorVisible = true"
    />
    <button-scale />
    <slot name="append" />
    <main-toolbar-menu />

    <custom-recipe-panel
        v-model="customRecipePanelVisible"
        @recipes-changed="onRecipesChanged"
    />
    <unified-recipe-editor
        v-model="unifiedRecipeEditorVisible"
        @recipes-changed="onRecipesChanged"
    />
</template>
