import type {GameDataSerialized, GameItemSerialized} from '#types/game-data-serialized';

/**
 * Merges extension items into base game data
 * Returns a new GameDataSerialized with extension items merged
 *
 * Note: Only merges items, not recipes or recipe dictionaries
 */
export function mergeExtensionItems(
    baseData: GameDataSerialized,
    extensionData: { items: GameItemSerialized[] }
): GameDataSerialized {
    const mergedItems = [...baseData.items];

    // Add items (check for duplicates by name)
    for (const item of extensionData.items) {
        if (!mergedItems.find(i => i.name === item.name)) {
            mergedItems.push(item);
        }
    }

    return {
        ...baseData,
        items: mergedItems,
    };
}

/**
 * Merges extension images into base game images
 * Returns a new GameImages object with extension images merged
 */
export function mergeExtensionImages(
    baseImages: Record<string, number[]>,
    extensionImages: Record<string, Record<string, number[]>>
): Record<string, number[]> {
    const merged = {...baseImages};

    for (const [extId, images] of Object.entries(extensionImages)) {
        Object.assign(merged, images);
    }

    return merged;
}
